<template>

  <!-- Label -->
  <DataSourceMapping
      label="Label DataSource"
      name="Label"
      :model="layer.ui.label"
      :mapping="labelDataSourceMapping"
      :dataSources="dataSources"
      :transformFunc="labelTransformFunc"
  ></DataSourceMapping>

  <v-text-field variant="solo" density="compact" label="Description"
                v-model="layer.description"></v-text-field>

  <!-- Location -->
  <DataSourceMapping
    label="Location DataSource"
    name="Location"
    :model="layer.ui.location"
    :mapping="locationDataSourceMapping"
    :dataSources="dataSources"
    :transformFunc="locationTransformFunc"
  ></DataSourceMapping>

  <!-- Orientation -->
  <DataSourceMapping
      label="Orientation DataSource"
      name="Orientation"
      :model="layer.ui.orientation"
      :mapping="orientationDataSourceMapping"
      :dataSources="dataSources"
      :transformFunc="orientationTransformFunc"
  ></DataSourceMapping>

  <!-- MarkerId -->
  <DataSourceMapping
      label="MarkerId DataSource"
      name="MarkerId"
      :model="layer.ui.markerId"
      :mapping="markerIdDataSourceMapping"
      :dataSources="dataSources"
      :transformFunc="markerIdTransformFunc"
  ></DataSourceMapping>

  <ColorPicker v-model="layer.labelColor" label="Label color"></ColorPicker>
  <ColorPicker v-model="layer.labelOutlineColor"  label="Label outline color"></ColorPicker>
  <ColorPicker v-model="layer.labelBackgroundColor" label="Label background color"></ColorPicker>

  <VTextFieldNumber
    label="Label size"
    v-model="layer.labelSize"
  ></VTextFieldNumber>
  <div class="label-anchor">
    <VTextFieldNumber
        label="Label Anchor X"
        v-model="layer.labelOffset[0]"
    ></VTextFieldNumber>
    <VTextFieldNumber
        label="Label Anchor Y"
        v-model="layer.labelOffset[1]"
    ></VTextFieldNumber>
  </div>
  <v-switch
      color="info"
      label="Url"
      v-model="layer.ui.urlIcon"
      density="compact"
  ></v-switch>
  <template v-if="layer.ui.urlIcon">
    <v-text-field variant="solo" density="compact" label="Model" v-model="layer.icon"></v-text-field>
  </template>
  <template v-else>
    <MdiIconPicker @onSelect="onSelectIcon" :defaultIcon="icon" label="Icon"></MdiIconPicker>
  </template>
  <v-slider
      v-model="layer.iconScale"
      :max="5.0"
      min="0.1"
      :step="0.1"
      show-ticks="always"
      label="Icon scale"
      hide-details
      class="slider"
      density="compact"
      variant="solo"
  >
    <template v-slot:append>
      <v-text-field
          v-model="layer.iconScale"
          type="number"
          style="width: 85px"
          density="compact"
          hide-details
          variant="outlined"
      ></v-text-field>
    </template>
  </v-slider>
  <ColorPicker v-model="layer.iconColor"  label="Icon color"></ColorPicker>
  <div class="icon-anchor">
    <VTextFieldNumber
        label="Icon Anchor X"
        v-model="layer.iconAnchor[0]"
    ></VTextFieldNumber>
    <VTextFieldNumber
        label="Icon Anchor Y"
        v-model="layer.iconAnchor[1]"
    ></VTextFieldNumber>
  </div>
</template>
<script>

import MdiIconPicker from '@/components/MdiIconPicker.vue';
import {inject, reactive} from "vue";
import ColorPicker from "@/components/ColorPicker.vue";
import {isDefined} from 'osh-js/core/utils/Utils';
import {useSweStore} from "@/store/swe";
import {useRoute} from "vue-router/dist/vue-router";
import {stringifyFn} from "@/utils";
import DataSourceMapping from "@/views/forms/widget/DataSourceMapping";
import VTextFieldNumber from '@/components/VTextFieldNumber';

function defaultCesiumViewLayer() {
  return {
    icon: 'https://storage.googleapis.com/grx-morpheusx-icons/mdi-svg/map-marker.svg',
    iconScale: 1.0,
    zoomLevel:  10,
    iconColor: '#000000CC',
    iconAnchor: [16, 32],
    class: 'PointMarkerLayer',
    labelOffset: [5, 10],
    labelSize: 12,
    labelColor: undefined,
    labelOutlineColor: undefined,
    labelBackgroundColor: undefined,
  }
}

function defaultOpenLayerViewLayer() {
  return {
    icon: 'https://storage.googleapis.com/grx-morpheusx-icons/mdi-svg/map-marker.svg',
    iconSize: [16, 16],
    iconAnchor: [16, 32],
    labelOffset: [5, 10],
    iconScale: 1.0,
    zoomLevel:  10,
    iconColor: '#000000CC',
    class: 'PointMarkerLayer'
  }
}

export default {
  name: "WidgetFormPointMarkerLayer",
  components: {
    DataSourceMapping,
    ColorPicker,
    MdiIconPicker,
    VTextFieldNumber,
  },
  setup(props) {
    const sweStore = useSweStore();
    const route = useRoute();

    const dataSources = inject('dataSources');
    let layer = inject('reactiveLayer');
    const widget = inject('widget');
    if(!layer.class) {
      if (widget.name.toLowerCase().startsWith('cesium')) {
        Object.assign(layer, defaultCesiumViewLayer());
      } else if (widget.name.toLowerCase() === 'openlayer') {
        Object.assign(layer, defaultOpenLayerViewLayer());
      }
    }
    if(!layer.ui.location) {
      layer.ui.label = {
        dataSourceId: undefined,
        data: undefined
      };

      layer.ui.location = {
        dataSourceId: undefined,
        x: undefined,
        y: undefined,
        z: undefined
      };
      layer.ui.orientation = {
        dataSourceId: undefined,
        heading: undefined
      };
      layer.ui.markerId = {
        dataSourceId: undefined,
        markerId: undefined
      };

      layer.ui.urlIcon = false;
    }
    let icon;
    if('icon' in layer) {
      icon = layer.icon.split('/')
      icon = icon[icon.length - 1].split('.svg')[0];
    } else {
      icon = 'https://storage.googleapis.com/grx-morpheusx-icons/mdi-svg/map-marker.svg'.split('/')[icon.length - 1].split('.svg')[0];
    }

    const {dashboardId } = route.params;

    return {layer, icon, sweStore, dataSources, dashboardId};
  },
  computed: {
    labelDataSourceMapping() {
      return this.getMapping(this.layer.ui.label.dataSourceId);
    },
    locationDataSourceMapping() {
      return this.getMapping(this.layer.ui.location.dataSourceId);
    },
    orientationDataSourceMapping() {
      return this.getMapping(this.layer.ui.orientation.dataSourceId);
    },
    markerIdDataSourceMapping() {
      return this.getMapping(this.layer.ui.markerId.dataSourceId);
    }
  },
  watch: {
    'layer.ui.label.data': {
      handler(val) {
        this.updateFunc();
      }
    },
    'layer.ui.label.getLabel': {
      handler(val) {
        this.updateFunc();
      }
    },
    'labelDataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.label = {
            dataSourceId: undefined,
            data: undefined,
          };
        }
      },
    },
    'layer.ui.location.x': {
      handler(val) {
        this.updateFunc();
      }
    },
    'layer.ui.location.y': {
      handler(val) {
        this.updateFunc();
      }
    },
    'layer.ui.location.z': {
      handler(val) {
        this.updateFunc();
      }
    },
    'layer.ui.location.getLocation': {
      handler(val) {
        this.updateFunc();
      }
    },
    'locationDataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.location = {
            dataSourceId: undefined,
            x: undefined,
            y: undefined,
            z: undefined,
          };
        }
      },
    },
    'orientationDataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.orientation = {
            dataSourceId: undefined,
            heading: undefined
          };
        }
      },
    },
    'layer.ui.orientation.heading': {
      handler(val) {
        this.updateFunc();
      }
    },
    'layer.ui.orientation.getOrientation': {
      handler(val) {
        this.updateFunc();
      }
    },
    'markerIdDataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.markerId = {
            dataSourceId: undefined,
            markerId: undefined
          };
        }
      },
    },
    'layer.ui.markerId.markerId': {
      handler(val) {
        this.updateFunc();
      }
    },
  },
  methods: {
    onSelectIcon(icon) {
      this.layer.icon = `https://storage.googleapis.com/grx-morpheusx-icons/mdi-svg/${icon}.svg`;
    },
    updateFunc() {
      if(
          this.layer.ui.location.x &&
          this.layer.ui.location.y &&
          this.layer.ui.location.z
      ) {
        let labelFnAsStr, locationFnAsStr, orientationFnAsStr, markerIdFnAsStr;

        if(this.layer.ui.location.getLocation) {
          locationFnAsStr = this.layer.ui.location.getLocation;
        } else {
          // compute one
          locationFnAsStr = this.locationTransformFunc(this.layer.ui.location);
        }

        if(this.layer.ui.orientation.getOrientation) {
          orientationFnAsStr = this.layer.ui.orientation.getOrientation;
        } else {
          // compute one
          orientationFnAsStr = this.orientationTransformFunc(this.layer.ui.orientation);
        }

        if(this.layer.ui.markerId.getMarkerId) {
          markerIdFnAsStr = this.layer.ui.markerId.getMarkerId;
        } else {
          // compute one
          markerIdFnAsStr = this.markerIdTransformFunc(this.layer.ui.markerId);
        }

        if(this.layer.ui.label.getLabel) {
          labelFnAsStr = this.layer.ui.label.getLabel;
        } else {
          // compute one
          labelFnAsStr = this.labelTransformFunc(this.layer.ui.label);
        }

        let func = '';

        if(locationFnAsStr && locationFnAsStr !== '') {
          func = locationFnAsStr;
        }

        if(orientationFnAsStr && orientationFnAsStr !== '') {
          func = func.slice(0, -2) + ',' + orientationFnAsStr.slice(1);
        }

        if(markerIdFnAsStr && markerIdFnAsStr !== '') {
          func =  func.slice(0, -2) + ',' + markerIdFnAsStr.slice(1);
        }

        if(labelFnAsStr && labelFnAsStr !== '') {
          func =  func.slice(0, -2) + ',' + labelFnAsStr.slice(1);
        }

        this.layer['marker@func'] = func;
      }
    },
    locationTransformFunc(model) {
      let x,y,z;

      x = (typeof model.x === 'object')? `rec${model.x.data.path.replaceAll('\/', '.')}`: `${model.x}`;
      y = (typeof model.y === 'object')? `rec${model.y.data.path.replaceAll('\/', '.')}`: `${model.y}`;
      z = (typeof model.z === 'object')? `rec${model.z.data.path.replaceAll('\/', '.')}`: `${model.z}`;

      const func = {
        getLocation: {
          dataSourceIds: [model.dataSourceId],
          handler: function (rec) {
            return {
              x: $x$,
              y: $y$,
              z: $z$
            };
          }
        }
      };
      let fnAsStr = stringifyFn(func);

      fnAsStr = fnAsStr.replace('$x$', x);
      fnAsStr = fnAsStr.replace('$y$', y);
      fnAsStr = fnAsStr.replace('$z$', z);

      return fnAsStr;
    },

    orientationTransformFunc(model) {
      let fnAsStr = '';
      const isOrientation = model.heading;

      if(isOrientation) {
        let heading = (typeof model.heading === 'object') ? `rec${model.heading.data.path.replaceAll('\/', '.')}` : `${model.heading}`;
        const func = {
          getOrientation: {
            dataSourceIds: [model.dataSourceId],
            handler: function (rec) {
              return {
                heading: $heading$,
              };
            }
          }
        };
        fnAsStr = stringifyFn(func);
        fnAsStr = fnAsStr.replace('$heading$', heading);
      }
      return fnAsStr;
    },

    markerIdTransformFunc(model) {
      let fnAsStr = '';

      const isMarkerId = model.markerId;
      if(isMarkerId) {
        let markerId = (typeof model.markerId === 'object') ? `rec${model.markerId.data.path.replaceAll('\/', '.')}` : `${model.markerId}`;
        const func = {
          getMarkerId: {
            dataSourceIds: [model.dataSourceId],
            handler: function (rec) {
              return $markerId$;
            }
          }
        };

        fnAsStr = stringifyFn(func);
        fnAsStr = fnAsStr.replace('$markerId$', markerId);
      }
      return fnAsStr;
    },

    labelTransformFunc(model) {
      let fnAsStr = '';

      let label = (typeof model.data === 'object') ? `'' + rec${model.data.data.path.replaceAll('\/', '.')}` : `${model.data}`;
      const func = {
        getLabel: {
          dataSourceIds: [model.dataSourceId],
          handler: function (rec) {
            return $label$;
          }
        }
      };

      fnAsStr = stringifyFn(func);
      fnAsStr = fnAsStr.replace('$label$', label);
      return fnAsStr;
    },

    getMapping(id) {
      let values = reactive({value: []});
      if(id) {
        this.sweStore.getDataSourceMapping(this.dashboardId, id).then(result => {
          values.value = result;
        });
      }
      return values;
    },
  }
}
</script>

<style>
.v-tooltip .v-overlay__content {
  pointer-events: auto;
  background: rgba(66,66,66,0.9);
}
</style>

<style scoped>
.info-icon {
  margin-right: 10px;
  cursor: pointer;
  z-index: 999999999999;
}

.slider {
  padding-top: 5px;
  overflow: hidden;
}
.icon-anchor, .label-anchor {
  display: flex;
}
</style>
