<template>
  <v-snackbar
      v-model="uiStore.ui.snack.message"
      :color="uiStore.ui.snack.type"
  >
    {{ uiStore.ui.snack.message }}
    <template v-slot:actions>
      <v-btn
          color="dark"
          variant="text"
          @click="uiStore.ui.snack.message = undefined"
      >
        Close
      </v-btn>
    </template>
  </v-snackbar>
  <router-view :key="$route.params.dashboardId"/>
</template>

<script>
import {useClientStore} from "@/store/client";
import {useUiStore} from "@/store/ui";
import {isDefined} from 'osh-js/core/utils/Utils';
import {usePopupsStore} from "@/store/popups";
import {useRoute} from "vue-router/dist/vue-router";

export default {
  name: "App",
  setup() {
    // stores
    const clientStore = useClientStore();
    const uiStore = useUiStore();
    const popupsStore = usePopupsStore();

    const isPopup = isDefined(window.opener);
    // clientStore.loadConfFromLocalStorage(isPopup);
    // clientStore.loadConfFromSercer(isPopup);

    return {uiStore, clientStore,popupsStore, isPopup};
  },
  created() {
    if(!this.isPopup) {
      window.addEventListener('beforeunload', (event) => {
        // This method will be called when the user leaves the page or closes the tab

        // Optionally, you can show a confirmation dialog to the user
        event.preventDefault();
        this.popupsStore.closePopups();
        event.returnValue = '';

      });
    }
  },
}
</script>
<style>

  h2 {
    font-size: 1.25em;
  }

  /* removing bottom padding for all textfields */
  .v-input--density-compact .v-field--no-label, .v-input--density-compact .v-field--label {
    --v-field-padding-bottom: 0px;
  }

  .v-input {
    overflow: auto;
  }
</style>



