<template>
      <v-select
          label="Type"
          :items="items"
          variant="solo"
          @update:modelValue="handleSelect"
          v-model="selected"
          density="compact"
      ></v-select>
  <v-slider
      v-model="properties.refreshRate"
      :max="5000"
      min="250"
      :step="250"
      show-ticks="always"
      label="Refresh Rate"
      hide-details
      class="ma-0 refreshRate"
      density="compact"
      variant="solo"
  >
    <template v-slot:append>
      <v-text-field
          v-model="properties.refreshRate"
          type="number"
          style="width: 85px"
          density="compact"
          hide-details
          variant="outlined"
      ></v-text-field>
    </template>
  </v-slider>
</template>

<script>
import {isDefined} from 'osh-js/core/utils/Utils';
import {inject, toRaw} from "vue";

export default {
  name: "WidgetFormChart",
  data () {
    return {
      items: ['Curve'],
      selected: undefined,
      properties: {
        refreshRate: 250
      }
    }
  },
  setup(props) {
    const widget = inject('widget');
    return { widget };
  },
  beforeMount() {
    this.widget.supportedLayers = ['curve'];
    this.widget.description= this.widget.description || 'Chart View';
    this.widget.description= 'Chart view';

    if(isDefined(this.widget.subType)) {
      this.selected = this.widget.subType;
      this.properties = this.widget.properties;
    } else {
      this.widget.properties = this.properties;
    }
  },
  methods: {
    handleSelect(event) {
      this.widget.layers = [];
      this.widget.subType = event;
      this.widget.type = 'ChartView';
      this.widget.name = 'ChartView';
    },
  }
}
</script>
<style scoped>
.refreshRate {
  padding-top: 5px;
  overflow: hidden;
}
</style>

