<template>
  <v-dialog
      v-model="showDialog"
      :close-on-content-click="false"
      :nudge-right="40"
      transition="scale-transition"
      offset-y
      max-width="350px"
  >
    <template v-slot:activator="{ props }">
        <v-text-field
            v-model="selectedColor"
            :label="label"
            v-bind="props"
            readonly
            variant="solo"
            density="compact"
            class="input-text"
        >
          <template v-slot:prepend-inner>
            <v-avatar
                :color="selectedColor"
                density="compact"
                size="small"
            ></v-avatar>
          </template>
        </v-text-field>
    </template>
    <v-card>
      <v-container class="d-flex justify-md-space-between">
        <v-color-picker show-swatches v-model="selectedColor" mode="rgba" width="300"></v-color-picker>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  name: "ColorPicker",
  props: {
    modelValue: String,
    label: {
      type: String,
      default: () => 'Color'
    }
  },
  emits: ['update:modelValue','onSelect'],
  data() {
    return {
      loading: false,
      showDialog: false,
      selectedColor: this.modelValue || '#000000'
    }
  },
  watch: {
    selectedColor() {
      this.$emit('onSelect', this.selectedColor);
      this.$emit('update:modelValue', this.selectedColor);
    },
  }
}
</script>

<style scoped>
i.v-icon.v-icon {
  color: grey !important;
}

.hidden {
  display: none;
}

.container {
  display:flex;
}

@keyframes fade {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@media (max-width: 480px) {
  .dots div {
    width: 0.7em;
    height: 0.7em;
  }
}

</style>
