import { createApp, watch } from 'vue'
import App from './App.vue'
import router from './router'
import { createPinia, useStore } from 'pinia'
import vuetify from './plugins/vuetify'
import * as devalue from 'devalue'
import {useClientStore} from "./store/client";
import {useUiStore} from "./store/ui";
import mitt from 'mitt';

export const emitter = mitt();

const app = createApp(App);
app.config.unwrapInjectedRef = false;
app.config.globalProperties.emitter = emitter;
const pinia = createPinia();

window.pinia = pinia;
const clientStore = useClientStore(pinia);
const uiStore = useUiStore(pinia);

if (window.opener && window.opener.pinia) {
  // init clientStore from main window
  clientStore.$patch(window.opener.pinia.state.value[clientStore.$id]);
  uiStore.$patch(window.opener.pinia.state.value[uiStore.$id]);
}

function merge (target, source) {
  if(Array.isArray(source)) {
    target = source;
  } else {
    // Iterate through `source` properties and if an `Object` set property to merge of `target` and `source` properties
    for (const key of Object.keys(source)) {
      if (source[key] instanceof Object && key in target){
        Object.assign(source[key], merge(target[key], source[key]));
      }
    }

    // Join `target` and modified `source`
    Object.assign(target || {}, source)
  }
  return target
};

function makeStoreReactive(store, key) {
  const channel = new BroadcastChannel('STORE_SYNC-'+store.$id);
  let lastVersion = -1;
  watch(
      () => store.syncVersion,
      // () => store.$state,
      (newVersion) => {
        if (newVersion > lastVersion) {
          lastVersion = newVersion;
          channel.postMessage({
            id: store.id,
            version: newVersion,
            state: devalue.parse(devalue.stringify(store.$state)),
          });
        }
      },
      {deep: true},
  );

  channel.onmessage = (e) => {
    const evt = e.data;
    if(evt.state.id === store.id) {
      merge(store, evt.state);
    }

  };
}

if (window.opener && window.opener.pinia) {
  makeStoreReactive(uiStore);
  makeStoreReactive(window.opener.pinia.state.value[clientStore.$id]);
  // makeStoreReactive(clientStore);
} else {
  makeStoreReactive(clientStore);
  makeStoreReactive(uiStore);
}

app.config.unwrapInjectedRef = true;
app.use(pinia);
app.use(router);
app.use(vuetify)
app.mount('#app');
