<template>
  <v-switch
      color="info"
      label="Show Stats"
      v-model="properties.showStats"
      density="compact"
  >
  </v-switch>
  <v-switch
      color="info"
      label="Show Time"
      v-model="properties.showTime"
      density="compact"
  >
  </v-switch>
</template>

<script>
import {inject} from "vue";

export default {
  name: "WidgetFormVideo",
  data () {
    return {
      properties: {
        showStats: true,
        showTime: true,
        css: 'video-css'
      }
    }
  },
  setup(props) {
    const widget = inject('widget');
    return { widget };
  },
  mounted() {
    this.widget.supportedLayers = ['videoData'];
    this.widget.type = 'VideoView';
    this.widget.name = 'video';
    this.widget.description= 'Video view';

    this.widget.properties = this.properties;
  }
}
</script>

