<template>
  <v-app class="dashboard">
    <navbar :popup="isPopup"/>
    <v-navigation-drawer
        v-model="uiStore.ui.drawer.server"
        temporary
        class="nav-drawer"
    >
      <ServerList></ServerList>
    </v-navigation-drawer>
    <v-main class="main">
      <Content :key="$route.params.fullPath" v-if="init"></Content>
      <v-progress-circular
          :size="100"
          :width="5"
          indeterminate
          color="teal"
          v-else
      >
      </v-progress-circular>
    </v-main>
  </v-app>
</template>

<script>
import Content from '@/views/Content.vue';
import Navbar from "@/components/NavBar.vue";
import {useRoute} from "vue-router";
import {useClientStore} from "@/store/client";
import {defaultWindow} from "@/store/defaults";
import {markRaw, provide, toRaw} from "vue";
import {toArray} from "@/utils";
import {randomUUID, isDefined} from 'osh-js/core/utils/Utils';
import {usePopupsStore} from "@/store/popups";
import {useUiStore} from "@/store/ui";
import Window from "@/morpheusx/Window.vue";
import ServerList from "@/views/ServerList";
import {EventType} from 'osh-js/core/event/EventType';
import {Mode} from 'osh-js/core/datasource/Mode';

export default {
  name: "Dashboard",
  components: {
    ServerList,
    Content,
    Window,
    Navbar
  },
  beforeRouteEnter(to, from, next) {
    function createNewWindow(windowId = randomUUID()) {
      let windowState = markRaw(defaultWindow(windowId, isDefined(window.opener) ? 'popup' : 'main-window'));
      uiStore.addWindow(dashboardId, windowState);
      console.log(`created new window with id ${windowState.id}`);
      return windowState;
    }

    const clientStore = useClientStore();
    const uiStore = useUiStore();
    const {dashboardId, windowId} = to.params;

    let redirect = !isDefined(windowId);

    const isPopup = isDefined(window.opener);
    clientStore.getDashboardByIdFromRemote(dashboardId, isPopup).then(dashboard => {
      if (!isDefined(dashboard) || !isDefined(dashboard.windows)) {
        console.log(`creating new dashboard with id ${dashboardId}`);
        clientStore.createNewDashboard(dashboardId, dashboard.name);
      }

      // case 1: dashboard + no existing main window
      // case 2: dashboard + existing main window
      // case 3: dashboard + no existing popup
      // case 4: dashboard + existing popup

      let win = clientStore.getWindowById(dashboardId, windowId);
      if (!win) {
        // check if main window
        if (!window.opener) {
          // in that case, we can check if a main window already exist and load it
          const windows = clientStore.getWindowsByType(dashboardId, 'main-window');
          if (windows.length > 0) {
            console.log(`Find existing main window with id ${windows[0].id}`);
            win = windows[0];
            redirect = true;
          }
        }
      }
      if (!win) {
        win = createNewWindow(windowId);
      }

      if (redirect) {
        next({
          name: 'window',
          params: {dashboardId: dashboardId, windowId: win.id}
        });
      } else {
        next();
      }
    });
  },
  setup(props) {
    // stores
    const popupsStore = usePopupsStore();
    const clientStore = useClientStore();
    const uiStore = useUiStore();

    const isPopup = isDefined(window.opener);
    const route = useRoute();

    // check if the dashboard exists
    let {dashboardId, windowId} = route.params;
    let dashboard = clientStore.getDashboardById(dashboardId);

    // we do not link directly to the store to have the ability to cancel any change until to click onto SAVE button
    provide('windowId', windowId);

    // bind datasynchronizer to ui Store
    if(!isPopup) {
      const defaultDataSynchronizer = clientStore.getDefaultTimeSynchronizer(dashboardId);

      defaultDataSynchronizer.subscribe((message) => {
        if(defaultDataSynchronizer.getMode() === Mode.REAL_TIME) {
          switch (message.status) {
            case 'connected':
              uiStore.setConnected(true);
              break;
            case 'disconnected':
              uiStore.setConnected(false);
              break;
            case 'not_running':
              uiStore.setConnected(false);
              break;
          }
        }
      }, [EventType.STATUS]);

      defaultDataSynchronizer.onChangedMode = (mode) => {
        uiStore.ui.mode = mode;
        console.log('setting mode='+mode);
      }
    }
    return {isPopup, popupsStore, clientStore, uiStore, windowId, dashboard};
  },
  mounted() {
    this.uiStore.setEditMode(false);
    // this.uiStore.toggleConnect(this.dashboard.id, false);

    // TODO: this is bad
    if (!this.isPopup) {
      this.emitter.on("load", () => this.handleLoad());
      this.emitter.on("save", () => this.handleSave());
      this.emitter.on("reset", () => this.handleReset());
      this.emitter.on("add-popup", () => this.handleAddPopupWindow());
      this.emitter.on("toggleConnect", () => this.toggleConnect());

      this.handleLoad(toArray(this.dashboard.windows)).catch(console.error).then(this.init = true)
    } else {
      this.init = true
    }
  },
  beforeUnmount() {
    if (!this.isPopup) {
      // unsubscribe component events
      this.emitter.off("load");
      this.emitter.off("save");
      this.emitter.off("reset");
      this.emitter.off("add-popup");
      this.emitter.off("toggleConnect");
    }
  },
  data() {
    return {
      init: false
    }
  },
  methods: {
    toggleConnect() {
      this.uiStore.toggleConnect(this.dashboard.id);
    },
    closePopups() {
      this.popupsStore.closePopups();
    },
    savePopupsPosition() {
      this.popupsStore.savePopupsPosition(this.dashboard.id);
    },
    handleSave() {
      this.savePopupsPosition();
      this.clientStore.saveConf(this.dashboard.id);
      this.uiStore.setEditMode(false);
    },
    async handleLoad(windowsArr) {
      this.closePopups();
      // this.uiStore.loadLayouts(toArray(this.clientStore.clientConfig.windows));
      this.uiStore.loadLayouts(windowsArr);

      return new Promise(async resolve => {
        const popupsWindow = this.clientStore.getWindowsByType(this.dashboard.id, 'popup');
        for (let popupWindow of popupsWindow) {
          // open new popup
          const props = `width=${popupWindow.size.width}, height=${popupWindow.size.height}, screenX=${popupWindow.pos.x}, ` +
              `screenY=${popupWindow.pos.y}, ` +
              `directories=no,titlebar=no,scrollbars=no,resizable=yes,status=no,location=no, ` +
              `locationbar=no,toolbar=no,menubar=no`;
          if ('getScreenDetails' in window) {
            // The Multi-Screen Window Placement API is supported.
            console.log('Multi-Screen Window Placement API is supported');
            try {
              // console.log(await window.getScreenDetails());
              let granted = false;
              const {state} = await navigator.permissions.query({name: 'window-placement'});
              granted = state === 'granted';
            } catch (ex) {
              // Nothing.
              throw ex;
            }
          } else {
            console.warn('Multi-Screen Window Placement API is not supported');
          }
          const popup = this.openNewWindow(props, popupWindow.id);
          popup.moveTo(popupWindow.pos.x, popupWindow.pos.y);
          resolve();
        }
      });
    },
    handleReset(event) {
      this.closePopups();
      this.uiStore.reset(this.dashboard.id);
    },
    handleAddPopupWindow(event) {
      this.openNewWindow();
    },
    openNewWindow(props = 'width=800,' +
                  'height=800,' +
                  'resizable=yes,' +
                  'status=no,' +
                  'scrollbars=no,' +
                  'statusbar=no,' +
                  'sidebar=no,' +
                  'location=no,' +
                  'locationbar=no,' +
                  'toolbar=no,' +
                  'menubar=no,' +
                  'personalbar=no',
                  id = randomUUID()
    ) {
      let routeData = this.$router.resolve({
        name: 'window',
        params: {
          dashboardId: this.dashboard.id,
          windowId: id
        }
      });
      const popupWindow = window.open(routeData.href, id, props);
      this.popupsStore.addPopupWindow(id, popupWindow);

      return popupWindow;
      // w.beforeunload = (event) => {
      //   alert("new window closed");
      // }
    },
  }
}
</script>
<style>
.v-navigation-drawer  {
  width: auto !important;
}
</style>
<style scoped>
.v-navigation-drawer  {
  width: auto !important;
}
.dashboard {
  background: white;
}
</style>

<style>
.main {
  align-items: center;
  justify-content: center;
  display: flex;
}

.main > div {
  height: 100%;
  width: 100%;
}

html, body {
  overflow: auto;
}

.video-css > canvas {
  width: 100%;
}
</style>
