<template>
  <v-dialog
      :model-value="true"
      class="dialog"
      content-class="content-dialog"
      persistent
  >
    <v-card
        elevation="24"
        class="mx-lg-n16 card form">
      <v-card-title v-if="title">
        <v-row align="center" justify="center">
          <v-col cols="12" lg="6" md="6"
                 class="grey lighten-2 fill-height d-flex flex-column justify-center align-center">
            <v-card flat tile>
              <v-card-text class="text-h6">{{ title }}</v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-card-title>
      <v-sheet
          class="sheet"
      >
        <div :class="'form-content ' + cssClass">
          <slot name="content"></slot>
        </div>
      </v-sheet>
      <v-card-actions class="actions">
        <v-btn
            icon
            dark
            @click="close"
            density="compact"
            class="cancel"
            variant="tonal"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar
            color="#e4e4e4"
            class="save-toolbar"
            density="compact"
        >
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
                variant="text"
                class="delete-btn"
                @click="deleteItemWithConfirm"
                v-if="edit"
            >
              Delete
            </v-btn>
            <v-btn
                variant="text"
                @click="save"
            >
              Save
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
      </v-card-actions>
    </v-card>
    <ConfirmDialog ref="confirm"/>
  </v-dialog>
</template>

<script>
import ConfirmDialog from "@/components/ConfirmDialog.vue";
import { provide, ref } from 'vue';

export default {
  name: "DialogForm",
  emits: ['onClose'],
  props: ['title','edit', 'cssClass', 'name'],
  setup(props, { attrs, slots, emit, expose }) {
    const title = props.title;
    const name = props.name || title;
    const callbacks =  {
      onSave: () => {},
      onDelete: () => {},
      onClose: () => {}
    }

    const close = () => {
      callbacks.onClose();
      emit('onClose');
    };
    const save = () => {
      callbacks.onSave();
      close();
    };
    const deleteItem = () => {
      callbacks.onDelete();
      close();
    };
    //
    provide('callbacks', callbacks);

    return {title, name, save, deleteItem, close};
  },
  components: {
    ConfirmDialog
  },
  methods: {
    async deleteItemWithConfirm() {
      if (
          await this.$refs.confirm.open(
              "Confirm",
              `Are you sure you want to remove this ${this.name} ?`
          )
      ) {
        this.deleteItem();
      }
    },
    cancel() {
      this.close();
    }
  }
}
</script>

