  overflow-y: auto;
}

.form-content {
  overflow: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 45px 20% 10px;
}

.sheet {
  overflow-y: auto;
}

.cancel {
  position: absolute;
  top: 15px;
  left: 15px;
}

.delete-btn {
  left: 0;
  position: absolute;
  color: red;
}
</style>
