<template>
  <v-select
      label="Data Source"
      :items="dataSources"
      item-title="name"
      item-value="id"
      variant="solo"
      v-model="layer.ui.frameData.dataSourceId"
      density="compact"
  >
  </v-select>

  <v-combobox
      :items="dataSourceMapping.value"
      label="frameData"
      variant="solo"
      density="compact"
      v-model="layer.ui.frameData.data"
      item-title="uiLabel"
      return-object
  >
    <template v-slot:selection="{ item }">
      <v-tooltip location="start" >
        <template v-slot:activator="{ props }">
          <v-icon v-bind="props" color="grey-lighten-1" class="info-icon">
            mdi-information
          </v-icon>
        </template>
        <span>
            <v-col cols="12" class="flex" justify="space-between">
              <template v-for="(item, key) in item.value.tooltip">
                <v-row><b>{{key}}</b>:
                  <template v-if="key === 'definition'"><a :href="item" target="_blank" rel="noopener noreferrer"> {{ item }} </a></template>
                  <template v-else> {{ item }}</template>
                </v-row>
              </template>
            </v-col>
          </span>
      </v-tooltip>
      {{ item.title }}
    </template>
    <template v-slot:item="data">
      <v-list-item v-bind="data.props">
        <template v-slot:title="{ title }">
          <v-tooltip location="start" >
            <template v-slot:activator="{ props }">
              <v-icon v-bind="props" color="grey-lighten-1" class="info-icon">
                mdi-information
              </v-icon>
            </template>
            <span>
            <v-col cols="12" class="flex" justify="space-between">
              <template v-for="(item, key) in data.item.value.tooltip">
                <v-row><b>{{key}}</b>:
                  <template v-if="key === 'definition'"><a :href="item" target="_blank" rel="noopener noreferrer"> {{ item }} </a></template>
                  <template v-else> {{ item }}</template>
                </v-row>
              </template>
            </v-col>
          </span>
          </v-tooltip>
          {{ title }}
        </template>
      </v-list-item>
    </template>
  </v-combobox>
</template>
<script>

import {inject, reactive} from "vue";
import {isDefined} from 'osh-js/core/utils/Utils';
import {useSweStore} from "@/store/swe";
import {useRoute} from "vue-router/dist/vue-router";
import {stringifyFn} from "@/utils";

export default {
  name: "WidgetFormVideoLayer",
  setup(props) {
    const sweStore = useSweStore();
    const route = useRoute();

    const {dashboardId } = route.params;
    const dataSources = inject('dataSources');
    const layer = inject('reactiveLayer');
    if(!layer.class) {
      layer.class = 'VideoDataLayer';
    }
    if(!layer.ui.frameData) {
      layer.ui.frameData = {
        dataSourceId: undefined,
        data: undefined
      };
    }

    return { layer, sweStore, dataSources, dashboardId };
  },
  computed: {
    dataSourceMapping() {
      let values = reactive({value: []});
      if(this.layer.ui.frameData.dataSourceId) {
        this.sweStore.getDataSourceMapping(this.dashboardId, this.layer.ui.frameData.dataSourceId).then(result => {
          values.value = result;
        });
      }
      return values;
    }
  },
  watch: {
    'layer.ui.frameData.data': {
      handler(val) {
        if (this.layer.ui.frameData.data) {
          let frameDataPath = (typeof this.layer.ui.frameData.data === 'object') ? `rec${this.layer.ui.frameData.data.data.path.replaceAll('\/', '.')}` : `${this.layer.ui.frameData.data}`;

          let fnAsStr = stringifyFn(
          {
            getFrameData: (rec) => $frameDataPath,
            getTimestamp: (rec) => rec.timestamp
          });
          fnAsStr = fnAsStr.replace('$frameDataPath', frameDataPath);

          this.layer['getFrameData@func'] = fnAsStr;
          this.layer.dataSourceId = this.layer.ui.frameData.dataSourceId;
        } else {
          this.layer.ui.frameData = {
            dataSourceId: undefined,
            data: undefined
          };
        }
      }, deep: true
    },
    'dataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.frameData = {
            dataSourceId: undefined,
            data: undefined
          };
        }
      }
    }
  },
}
</script>

<style>
.v-tooltip .v-overlay__content {
  pointer-events: auto;
  background: rgba(66,66,66,0.9);
}
</style>

<style scoped>
.info-icon {
  margin-right: 10px;
  cursor: pointer;
  z-index: 999999999999;
}
</style>
