<template>
  <v-app-bar app color="secondary" density="compact" elevation="3">
    <img alt="logo" src="../assets/logo-georobotix.png" style="max-height: 50%; margin-right: 10px; margin-left: 10px">
    <v-toolbar-title class="toolbar-title"><span>MorpheusX</span></v-toolbar-title>
    <v-spacer></v-spacer>
    <div class="actions">
      <v-tooltip bottom v-if="isRealTimeMode">
        <template v-slot:activator="{ props }" >
          <template v-if="!popup"></template>
          <v-btn
              rounded
              class="elevation-0"
              :icon='this.uiStore.ui.connected? "mdi-power-plug" : "mdi-power-plug-off"'
              :color='this.uiStore.ui.connected? "green" : "red"'
              v-bind="props"
              @click="toggleConnection"
              :disabled="!this.clientStore.initialized"
          >
          </v-btn>
        </template>
        <span> {{ this.uiStore.ui.connected ? "Disconnect" : "Connect" }}</span>
      </v-tooltip>
      <v-menu
          open-on-click
      >
        <template v-slot:activator="{ props }">
          <v-btn
              variant="text"
              depressed
              icon
              tile
              small
              v-bind="props"
          >
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </template>
        <v-list class="tile">
          <v-list-item
              @click="handleAddServer"
              append-icon="mdi-server-plus"
              density="compact"
              min-height="30"
              class="sub-tile"
              v-if="!popup"
          >
            <v-list-item-title>Server</v-list-item-title>
          </v-list-item>
          <v-list-item
              @click="handleAddDataSource"
              append-icon="mdi-database-plus"
              density="compact"
              min-height="30"
              class="sub-tile"
              v-if="!popup"
          >
            <v-list-item-title>DataSource</v-list-item-title>
          </v-list-item>
          <v-list-item
              @click="handleAddView"
              append-icon="mdi-form-select"
              density="compact"
              min-height="30"
              class="sub-tile"
          >
            <v-list-item-title>View</v-list-item-title>
          </v-list-item>
          <v-list-item
              @click="handleAddPopupWindow"
              append-icon="mdi-dock-window"
              density="compact"
              min-height="30"
              v-if="!popup"
              class="sub-tile"
          >
            <v-list-item-title>Window</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <v-menu
          open-on-click
          v-if="!popup"
      >
        <template v-slot:activator="{ props }">
          <v-btn
              variant="text"
              depressed
              icon
              tile
              small
              v-bind="props"
          >
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>
        <v-list class="tile">
          <v-list-item
              @click="handleServerDrawer"
              append-icon="mdi-server-network"
              density="compact"
              min-height="30"
              class="sub-tile"
          >
            <v-list-item-title>DataSources</v-list-item-title>
          </v-list-item>
          <v-list-item
              @click="handleEdit"
              append-icon="mdi-pencil"
              density="compact"
              min-height="30"
              class="sub-tile"
          >
            <v-list-item-title>Edit</v-list-item-title>
          </v-list-item>
          <v-list-item
              @click="handleSave"
              append-icon="mdi-content-save"
              density="compact"
              min-height="30"
              class="sub-tile"
          >
            <v-list-item-title>Save</v-list-item-title>
          </v-list-item>
          <!--v-list-item
              @click="handleLoad"
              append-icon="mdi-upload"
              density="compact"
              min-height="30"
          >
            <v-list-item-title>Load</v-list-item-title>
          </v-list-item-->
          <v-list-item
              @click="handleReset"
              append-icon="mdi-cancel"
              density="compact"
              min-height="30"
              class="sub-tile"
          >
            <v-list-item-title>Reset</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </div>
  </v-app-bar>
</template>

<script>
import {randomUUID, isDefined} from 'osh-js/core/utils/Utils';
import {useClientStore} from "@/store/client";
import {useUiStore} from "@/store/ui";

export default {
  components: {
  },
  data() {
    return {
      connected: false
    }
  },
  props: ['popup'],
  setup(props) {
    const uiStore = useUiStore();
    const clientStore = useClientStore();

    const popup = props.popup;
    return {uiStore, clientStore, popup};
  },
  computed: {
    isRealTimeMode() {
      return this.uiStore.ui.mode === 'realTime';
    }
  },
  methods: {
    handleServerDrawer(event) {
      this.uiStore.ui.drawer.server = true;
    },
    handleAddView(event) {
      this.emitter.emit('show-widget-editor', {id: this.uuid(), edit: false});
    },
    handleAddDataSource(event) {
      this.emitter.emit('show-datasource-editor', {id: randomUUID(), edit: false});
    },
    handleAddServer(event) {
      this.emitter.emit('show-server-editor', {id: randomUUID(), edit: false});
    },
    handleSave(event) {
      this.emitter.emit('save');
    },
    handleLoad(event) {
      this.emitter.emit('load');
    },
    handleReset(event) {
      this.emitter.emit('reset');
    },
    handleEdit(event) {
      this.uiStore.setEditMode(!this.uiStore.ui.edit);
    },
    handleAddPopupWindow(event) {
      this.emitter.emit('add-popup');
    },
    uuid() {
      return randomUUID();
    },
    toggleConnection() {
      this.emitter.emit('toggleConnect');
    },
  }
}
</script>

<style scoped>
.tile {
  background: white;
}

.sub-tile:hover {
  background: #e3e3e3;
}

.actions > button {
  margin: 2px;
}
.toolbar-title {
  margin-left: 0px;
  margin-top:2px;
  margin-inline-start: 0px !important;
}
</style>
