<template>
  <v-text-field variant="solo" density="compact" label="Name" v-model="props.name" ></v-text-field>
  <v-text-field variant="solo" density="compact" label="Endpoint" v-model="props.endpoint" ></v-text-field>
  <v-text-field variant="solo" density="compact" label="Description" v-model="props.description" ></v-text-field>
  <v-switch
      color="info"
      label="TLS"
      v-model="props.tls"
      density="compact"
  ></v-switch>
</template>

<script>
import {randomUUID, isDefined} from 'osh-js/core/utils/Utils';
import {useClientStore} from "@/store/client";
import {useRoute} from "vue-router";
import ConfirmDialog from "@/components/ConfirmDialog.vue";

export default {
  name: "ServerForm",
  props: ['id'],
  emits: ['onClose'],
  inject: ['callbacks'],
  components: {
    ConfirmDialog
  },
  setup(props) {
    // stores
    const clientStore = useClientStore();
    const route = useRoute();
    // check if the dashboard exists
    let {dashboardId} = route.params;

    return {dashboardId, clientStore};
  },
  data() {
    return {
      props: {
        id: this.id || randomUUID(),
        name: undefined,
        endpoint: undefined,
        tls: true,
        type: undefined,
        description: '',
        tags: [],
        dataSources: []
      },
      newServer : true
    }
  },
  beforeMount() {
    // find existing server
    const server = this.clientStore.findServerById(this.dashboardId, this.id);
    if(server) {
      this.props = { ...this.props, ...server };
      this.newServer = false;
    }
    this.callbacks.onSave = this.save;
    this.callbacks.onDelete = this.deleteServer;
  },
  methods: {
    save() {
      if(this.newServer) {
        console.log(`adding server ${this.dashboardId}, ${this.props.name}`);
        this.clientStore.addServer(this.dashboardId, this.props);
      } else {
        console.log(`editing server ${this.id}, ${this.props.name}`);
        this.clientStore.editServer(this.id,this.props);
      }
    },
    async deleteServer() {
      console.log('removing ' + this.props.id + ', '+this.props.name);
      this.clientStore.removeServer(this.dashboardId, this.props.id);
    },
  }
}
</script>
