<template>
  <v-select
      label="Data Source"
      :items="dataSources"
      item-title="name"
      item-value="id"
      variant="solo"
      v-model="layer.ui.values.dataSourceId"
      density="compact"
  >
  </v-select>
  <v-combobox
      :items="dataSourceMapping.value"
      label="x"
      variant="solo"
      density="compact"
      v-model="layer.ui.values.x"
      item-title="uiLabel"
      return-object
  >
    <template v-slot:selection="{ item }">
      <v-tooltip>
        <template v-slot:activator="{ props }">
          <v-icon v-bind="props" color="grey-lighten-1" class="info-icon">
            mdi-information
          </v-icon>
        </template>
        <span>
            <v-col cols="12" class="flex" justify="space-between">
              <template v-for="(item, key) in item.value.tooltip">
                <v-row><b>{{key}}</b>:
                  <template v-if="key === 'definition'"><a :href="item" target="_blank" rel="noopener noreferrer"> {{ item }} </a></template>
                  <template v-else> {{ item }}</template>
                </v-row>
              </template>
            </v-col>
          </span>
      </v-tooltip>
      {{ item.title }}
    </template>
    <template v-slot:item="data">
      <v-list-item v-bind="data.props">
        <template v-slot:title="{ title }">
          <v-tooltip>
            <template v-slot:activator="{ props }">
              <v-icon v-bind="props" color="grey-lighten-1" class="info-icon">
                mdi-information
              </v-icon>
            </template>
            <span>
            <v-col cols="12" class="flex" justify="space-between">
              <template v-for="(item, key) in data.item.value.tooltip">
                <v-row><b>{{key}}</b>:
                  <template v-if="key === 'definition'"><a :href="item" target="_blank" rel="noopener noreferrer"> {{ item }} </a></template>
                  <template v-else> {{ item }}</template>
                </v-row>
              </template>
            </v-col>
          </span>
          </v-tooltip>
          {{ title }}
        </template>
      </v-list-item>
    </template>
  </v-combobox>
  <v-combobox
      :items="dataSourceMapping.value"
      label="y"
      variant="solo"
      density="compact"
      v-model="layer.ui.values.y"
      item-title="uiLabel"
      return-object
  >
    <template v-slot:selection="{ item }">
      <v-tooltip>
        <template v-slot:activator="{ props }">
          <v-icon v-bind="props" color="grey-lighten-1" class="info-icon">
            mdi-information
          </v-icon>
        </template>
        <span>
            <v-col cols="12" class="flex" justify="space-between">
              <template v-for="(item, key) in item.value.tooltip">
                <v-row><b>{{key}}</b>:
                  <template v-if="key === 'definition'"><a :href="item" target="_blank" rel="noopener noreferrer"> {{ item }} </a></template>
                  <template v-else> {{ item }}</template>
                </v-row>
              </template>
            </v-col>
          </span>
      </v-tooltip>
      {{ item.title }}
    </template>
    <template v-slot:item="data">
      <v-list-item v-bind="data.props">
        <template v-slot:title="{ title }">
          <v-tooltip>
            <template v-slot:activator="{ props }">
              <v-icon v-bind="props" color="grey-lighten-1" class="info-icon">
                mdi-information
              </v-icon>
            </template>
            <span>
            <v-col cols="12" class="flex" justify="space-between">
              <template v-for="(item, key) in data.item.value.tooltip">
                <v-row><b>{{key}}</b>:
                  <template v-if="key === 'definition'"><a :href="item" target="_blank" rel="noopener noreferrer"> {{ item }} </a></template>
                  <template v-else> {{ item }}</template>
                </v-row>
              </template>
            </v-col>
          </span>
          </v-tooltip>
          {{ title }}
        </template>
      </v-list-item>
    </template>
  </v-combobox>
  <v-text-field variant="solo" density="compact" label="Legend" v-model="layer.name"></v-text-field>
  <v-text-field variant="solo" density="compact" label="Y axis label" v-model="layer.yLabel"></v-text-field>
  <v-text-field variant="solo" density="compact" label="Max values" v-model="layer.maxValues"></v-text-field>
  <v-text-field variant="solo" density="compact" label="Stroke width" v-model="layer.stroke"></v-text-field>
  <ColorPicker v-model="layer.lineColor" label="Line color"></ColorPicker>
  <ColorPicker v-model="layer.backgroundColor" label="Background color"></ColorPicker>
</template>
<script>

import {inject, reactive} from "vue";
import {isDefined} from 'osh-js/core/utils/Utils';
import ColorPicker from "@/components/ColorPicker.vue";
import {useRoute} from "vue-router/dist/vue-router";
import {useSweStore} from "@/store/swe";
import {stringifyFn} from "@/utils";

function defaultCurveLayer() {
  return {
    'getValues@func':  'return { x: rec.timestamp, y: rec.location.alt }',
    fill: true,
    backgroundColor: '#57c1ff80',
    lineColor: '#57c1ff80',
    maxValues: 25,
    stroke: 1,
    name: 'Chart',
    class: 'CurveLayer',
  }
}

export default {
  name: "WidgetFormCurveLayer",
  components: {
    ColorPicker
  },
  setup(props) {
    const sweStore = useSweStore();
    const route = useRoute();
    const { dashboardId } = route.params;

    let layer = inject('reactiveLayer');
    const dataSources = inject('dataSources');
    if(!layer.class) {
      Object.assign(layer, defaultCurveLayer());
    }
    if(!layer.ui.values) {
      layer.ui.values = {
        x: undefined,
        y: undefined,
        dataSourceId: undefined
      };
    }
    return { layer,dataSources,sweStore, dashboardId };
  },
  computed: {
    dataSourceMapping() {
      let values = reactive({value: []});
      if(this.layer.ui.values.dataSourceId) {
        this.sweStore.getDataSourceMapping(this.dashboardId, this.layer.ui.values.dataSourceId).then(result => {
          values.value = result;
        });
      }
      return values;
    }
  },
  watch: {
    'layer.maxValues': {
      handler(val) {
        this.layer.maxValues = parseInt(val);
      }
    },
    'layer.stroke': {
      handler(val) {
        this.layer.stroke = parseInt(val);
      }
    },
    'layer.ui.values.x': {
      handler(val) {
        this.updateFunc();
      }
    },
    'layer.ui.values.y': {
      handler(val) {
        this.updateFunc();
      }
    },
    'dataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.values = {
            x: undefined,
            y: undefined,
            dataSourceId: undefined
          };
        }
      }
    }
  },
  methods: {
    updateFunc() {
      if(this.layer.ui.values.x && this.layer.ui.values.y) {
        let x,y;
        x = (typeof this.layer.ui.values.x === 'object')? `rec${this.layer.ui.values.x.data.path.replaceAll('\/', '.')}`: `x: ${this.layer.ui.values.x}`;
        y = (typeof this.layer.ui.values.y === 'object')? `rec${this.layer.ui.values.y.data.path.replaceAll('\/', '.')}`: `y: ${this.layer.ui.values.y}`;
        const func = {
          getValues: {
            dataSourceIds: [this.layer.ui.values.dataSourceId],
            handler: function (rec) {
              return {
                x: $x$,
                y: $y$
              };
            }
          },
        };
        let fnAsStr = stringifyFn(func);

        fnAsStr = fnAsStr.replace('$x$', x);
        fnAsStr = fnAsStr.replace('$y$', y);
        this.layer['getValues@func'] = fnAsStr;
        // build label
        if(this.layer.ui.values.y && this.layer.ui.values.y.data.label && this.layer.ui.values.y.data.uom) {
          const yLabel = ` ${this.layer.ui.values.y.data.label} (${this.layer.ui.values.y.data.uom.code})`;
          this.layer.yLabel = yLabel;
          this.layer.name = this.layer.ui.values.y.dataSource.name + ' - ' + this.layer.ui.values.y.data.label;
        }
      }
    },
  }
}
</script>
<style>
.v-tooltip .v-overlay__content {
  pointer-events: auto;
  background: rgba(66,66,66,0.9);
}
</style>
<style scoped>
.info-icon {
  margin-right: 10px;
  cursor: pointer;
  z-index: 999999999999;
}
</style>
