<template>
  <v-select
      label="Server"
      :items="servers"
      variant="solo"
      density="compact"
      item-title="name"
      return-object
  >
    <template v-slot:selection="{ item }">
      <v-tooltip  location="start">
        <template v-slot:activator="{ props }">
          <v-icon v-bind="props" color="grey-lighten-1" class="info-icon-server">
            mdi-information
          </v-icon>
        </template>
        <span>
            <v-col cols="12" class="flex" justify="space-between">
              <v-row><b>id</b>: {{ item.raw.id }}</v-row>
              <v-row><b>name</b>: {{ item.raw.name }}</v-row>
              <v-row><b>endpoint</b>: {{ item.raw.endpoint }}</v-row>
              <v-row><b>tls</b>: {{ item.raw.tls }}</v-row>
            </v-col>
          </span>
      </v-tooltip>
      {{ item.title }}
    </template>
  </v-select>
</template>

<script>
import {useClientStore} from "@/store/client";
import {useRoute} from "vue-router/dist/vue-router";

export default {
  name: "Server",
  setup(props) {
    const clientStore = useClientStore();
    const route = useRoute();
    // check if the dashboard exists
    let {dashboardId} = route.params;
    const servers = clientStore.getServersByDashboard(dashboardId);
    return {servers};
  },
}
</script>


<style scoped>
.info-icon-server {
  margin-right: 10px;
}
</style>
