import CesiumView from "osh-js/core/ui/view/map/CesiumView";
import {
    Ellipsoid,
    Math as MathCesium, ScreenSpaceEventType
} from "cesium";
import Systems from "osh-js/core/sweapi/system/Systems";

class CesiumControlView extends CesiumView {
    constructor(properties) {
        super(properties);
        this.handleEvents();
        this.initControl = false;
    }

    async initSweApi() {
        if(!this.initControl) {

            console.log(this.properties.control)
            const systems = new Systems(this.properties.control);
            const system = await systems.getSystemById(this.properties.control.systemId);
            this.control = await system.getControlById(this.properties.control.cmdStreamId);
            this.initControl = true;
        }
    }

    handleEvents() {
        const onMouseClick = async (input) => {
            let cartesian = this.viewer.scene.pickPosition(input.position);
            const carto = Ellipsoid.WGS84.cartesianToCartographic(cartesian);
            const lonDegree = MathCesium.toDegrees(carto.longitude);
            const latDegree = MathCesium.toDegrees(carto.latitude);

            await this.postCommand(lonDegree, latDegree);
        }
        this.viewer.screenSpaceEventHandler.setInputAction(onMouseClick, ScreenSpaceEventType.LEFT_CLICK);
    }

    async postCommand(lon, lat) {
        let goToCmd = {
            params: {
                WAYPOINT: {
                    position: {
                        lat: lat,
                        lon: lon,
                        alt: 270
                    },
                    velocity: 100000
                }
            }
        }
        await this.initSweApi()
        await this.control.postCommand(JSON.stringify(goToCmd));
    }
}

export default CesiumControlView;
