export const defaultLayer = (id=randomUUID()) => {
    return {
        id: id,
        type: undefined,
        ui:{}
    }
};

export const defaultWidget = (id=randomUUID()) => {
    return {
        id: id,
        type: undefined,
        name: undefined,
        description: 'test view',
        pos: {
            x: 0,
            y: 0
        },
        size: {
            width: 4,
            height: 4
        },
        visible: true,
        tags: [],
        layers: []
    };
}

export const defaultWindow = (id=randomUUID(), type='main-window') => {
    return {
        "id": id,
        "type": type,
        "description": "window",
        "bk": undefined,
        "tags": [],
        "visible": true,
        "pos": {"x": 0, "y": 0, "z": 0},
        "size": {"width": 100, "height": 100, "unit": "%"},
        "widgets": {}
    };
};
