<template>
  <v-select
      label="Type"
      :items="widget.supportedLayers"
      variant="solo"
      v-model="reactiveLayer.type"
      density="compact"
  ></v-select>
  <slot v-if="reactiveLayer.type">
    <WidgetFormPointMarkerLayer v-if="reactiveLayer.type === 'marker'"></WidgetFormPointMarkerLayer>
    <WidgetFormVideoLayer v-else-if="reactiveLayer.type === 'videoData'"></WidgetFormVideoLayer>
    <WidgetFormCurveLayer v-else-if="reactiveLayer.type === 'curve'"></WidgetFormCurveLayer>
    <WidgetFormFrustumLayer v-else-if="reactiveLayer.type === 'frustum'"></WidgetFormFrustumLayer>
    <slot v-if="editing">
      <v-tooltip bottom>
        <template v-slot:activator="{ props }">
          <v-btn
              rounded
              class="elevation-6 add-layer-btn"
              icon="mdi-minus"
              color="red"
              v-bind="props"
              @click="removeFromWidget"
          >
          </v-btn>
        </template>
        <span>Remove to View</span>
      </v-tooltip>
    </slot>
    <slot v-else>
      <v-tooltip bottom>
        <template v-slot:activator="{ props }">
          <v-btn
              rounded
              class="elevation-6 add-layer-btn"
              icon="mdi-plus"
              v-bind="props"
              @click="addToWidget"
          >
          </v-btn>
        </template>
        <span>Add to View</span>
      </v-tooltip>
    </slot>
  </slot>
</template>

<script>

import WidgetFormPointMarkerLayer from "./WidgetFormPointMarkerLayer.vue";
import WidgetFormVideoLayer from "./WidgetFormVideoLayer.vue";
import {useClientStore} from "@/store/client";
import {computed, inject, markRaw, provide, reactive, ref, toRaw} from "vue";
import {useRoute} from "vue-router";
import WidgetFormCurveLayer from "./WidgetFormCurveLayer.vue";
import WidgetFormFrustumLayer from "@/views/forms/widget/layers/WidgetFormFrustumLayer";

export default {
  name: "WidgetFormLayers",
  components: {
    WidgetFormFrustumLayer,
    WidgetFormCurveLayer,
    WidgetFormVideoLayer,
    WidgetFormPointMarkerLayer
  },
  props: ['layer', 'editing'],
  setup(props) {
    const clientStore = useClientStore();
    const route = useRoute();

    const clientConfig = clientStore.clientConfig;
    const {widgetId, dashboardId, windowId} = route.params;

    const dataSources = [];
    for (let server of clientConfig.servers) {
      dataSources.push(...server.dataSources);
    }

    const widget = inject('widget');

    const reactiveLayer = reactive(props.layer);
    provide('reactiveLayer', reactiveLayer);
    provide('dataSources', dataSources);
    return {widget, reactiveLayer};
  },
  methods: {
    removeFromWidget(event) {
      const newLayers = [];
      for (let layer of this.widget.layers) {
        if (layer.id !== this.layer.id) {
          newLayers.push(layer);
        }
      }
      this.widget.layers = newLayers;
    },
    addToWidget(event) {
      this.widget.layers.push(this.layer);
    },
  }
}
</script>

<style scoped>
.actions-remove {
  display: flex;
  justify-content: space-around;
}

.add-layer-btn, .remove-layer-btn {
  width: 40px;
  height: 40px;
}
</style>

