// nodeList= 'recordSchema',
// scalarList = 'Quantity', 'Time', 'Category', 'Count', 'Boolean' , 'DataChoice'
import {klona} from "klona";

export function parseSweSchema(parent, node, results, path = '',
                               scalarList = new Set(['Quantity', 'Time', 'Category', 'Count', 'Boolean', 'DataChoice', 'DataArray', 'Text']),
                               skipList = new Set(['elementCount', 'elementType'])) {
    let currentPath = path;

    let relativeParent = Array.isArray(node)? parent: node;
    for (let eltKey in node) {
        let elt = node[eltKey];
        if(skipList.has(eltKey)) {
            break;
        }
        if(elt === 'Vector' || elt  === 'DataRecord') {
            if('name' in node) {
                currentPath += `/${node.name}`;
            }
        }
        if (scalarList.has(elt)) {
            const pClone = klona(node);
            pClone.path = currentPath + `/${node.name}`;
            if(!('label' in pClone) && 'label' in parent) {
                pClone.label = parent.label;
            }
            results.push(pClone);
        } else if (typeof elt === 'object') {
            parseSweSchema(relativeParent, elt, results, currentPath, scalarList, skipList);
        } else if (Array.isArray(elt)) {
            for (let e of elt) {
                parseSweSchema(relativeParent, e, results, currentPath,scalarList, skipList);
            }
        }
    }
}
