<template>
  <v-carousel
      progress="secondary"
      :continuous="false"
      class="caroussel"
      hide-delimiters
  >
    <v-carousel-item>
      <v-sheet
          class="sheet"
      >
        <div class="d-flex justify-center align-center title">
          <div class="text-h6">View</div>
        </div>
        <div class="form">
          <WidgetFormType></WidgetFormType>
        </div>
      </v-sheet>
    </v-carousel-item>
    <v-carousel-item v-for="(item, index) in widget.layers">
      <v-sheet
          class="sheet"
      >
        <div class="d-flex justify-center align-center title">
          <div class="text-h6">Layers</div>
          <v-badge :content="widget.layers.length" color="secondary" class="badge">
            <v-icon icon="mdi-layers" size="32px"></v-icon>
          </v-badge>
        </div>
        <div class="form">
          <WidgetFormLayers :key="item.id" :layer="item" :editing="true"></WidgetFormLayers>
        </div>
      </v-sheet>
    </v-carousel-item>
    <v-carousel-item>
      <v-sheet
          class="sheet"
      >
        <div class="d-flex justify-center align-center title">
          <div class="text-h6">Layers</div>
          <v-badge :content="widget.layers.length" color="secondary" class="badge">
            <v-icon icon="mdi-layers" size="32px"></v-icon>
          </v-badge>
        </div>
        <div class="form">
          <WidgetFormLayers :key="uuid()" ref="viewFormLayers" :layer="defaultLayer()"
                            :editing="false"></WidgetFormLayers>
        </div>
      </v-sheet>
    </v-carousel-item>
    <template v-slot:prev="{ props }">
      <v-btn
          variant="elevated"
          @click="props.onClick"
          icon="mdi-chevron-left"
          class="caroussel-left"
      ></v-btn>
    </template>
    <template v-slot:next="{ props }" >
      <v-btn
          variant="elevated"
          @click="props.onClick"
          icon="mdi-chevron-right"
          v-if="widget.uiType !== 'Timebar'"
      ></v-btn>
    </template>
  </v-carousel>
</template>

<script>
import WidgetFormType from "./WidgetFormType.vue";
import WidgetFormLayers from "./layers/WidgetFormLayers.vue";
import {reactive, provide} from "vue";
import {useClientStore} from "@/store/client";
import {useRoute} from "vue-router";
import {isDefined, randomUUID} from 'osh-js/core/utils/Utils';
import {useUiStore} from "@/store/ui";
import {defaultWidget, defaultLayer} from "@/store/defaults";
import {klona} from "klona";

export default {
  name: "WidgetForm",
  emits: ['onClose'],
  inject: ['callbacks'],
  props: ['id', 'serverId', 'edit'],
  components: {
    WidgetFormLayers,
    WidgetFormType,
  },
  setup(props) {
    const route = useRoute();
    const {dashboardId, windowId} = route.params;
    const clientStore = useClientStore();
    const uiStore = useUiStore();

    let widget = klona(clientStore.getWidgetById(dashboardId, windowId, props.id));
    if (!isDefined(widget)) {
      widget = defaultWidget();
    }

    widget.version = 0;
    widget.supportedLayers = [];

    widget = reactive(widget);
    provide('widget', widget);

    const formType = '';
    return {widget, clientStore, uiStore, windowId, dashboardId, formType};
  },
  beforeMount() {
    this.callbacks.onSave = this.save;
    this.callbacks.onDelete = this.deleteWidget;
  },
  methods: {
    defaultLayer() {
      return defaultLayer();
    },
    save() {
      console.log('Saving widget form..');
      console.log(this.widget)
      let window = this.clientStore.getWindowById(this.dashboardId, this.windowId);
      if (!this.edit) {
        this.uiStore.addGridItem(this.dashboardId, window, this.widget);
      } else {
        this.uiStore.editGridItem(this.dashboardId, window, this.widget);
      }
      this.uiStore.setEditMode(true);
    },
    uuid() {
      return randomUUID();
    },
    deleteWidget() {
      this.uiStore.removeGridItem(this.dashboardId, this.windowId, this.widget.id);
    }
  }
}
</script>

<style scoped>
.title {
  padding: 25px;
  font-size: 30px !important;
}

.badge {
  margin-left: 15px;
}

.form {
  padding: 5px 0 10px;
  max-width: 50%;
  margin: auto;
}
</style>
<style>

/* override default generic dialog padding  to move next/back button close to the border */

.mdi-circle::before {
  color: #4a6572;
}

.sheet {
  overflow: auto;
  height: 100%;
}
</style>
