<template>
  <v-form fast-fail>
    <Server v-model="state.server"></Server>
    <template v-if="state.server">
      <v-select
          label="Type"
          :items="types"
          variant="solo"
          density="compact"
          v-model="state.dataSource.type"
      >
      </v-select>
    </template>
    <template v-if="state.dataSource.type === 'SweApiStream'">
      <DataSourceSweForm :key="state.server"></DataSourceSweForm>
    </template>
  </v-form>
</template>
<script>

import {useClientStore} from "@/store/client";
import {useRoute} from "vue-router";
import DataSourceSweForm from "@/views/forms/datasource/DataSourceSweForm";
import Server from './Server';
import {DEFAULT_DATASOURCE} from "@/store/defaultConfig";
import {provide, reactive} from "vue";
import {klona} from "klona";
import {useUiStore} from "@/store/ui";

export default {
  name: "DataSourceForm",
  props: ['id', 'serverId', 'edit'],
  emits: ['onClose'],
  inject: ['callbacks'],
  components: {
    Server,
    DataSourceSweForm
  },
  setup(props) {
    // stores
    const clientStore = useClientStore();
    const uiStore = useUiStore();
    const route = useRoute();
    // check if the dashboard exists
    let {dashboardId} = route.params;

    const timeSynchronizerIds = clientStore.getTimeSynchronizerIds();

    const dataSourceId = props.id;
    const serverId = props.serverId;

    let dataSource = clientStore.findDataSourceById(dashboardId, dataSourceId);

    if (!dataSource) {
      dataSource = DEFAULT_DATASOURCE();
    } else {
      dataSource = klona(dataSource);
    }

    const server = clientStore.findServerById(dashboardId, serverId);

    const s = {
      server: server,
      dataSource: dataSource
    };


    const state = reactive(s);
    provide('state', state);

    const types = [
      {
        title: 'SWE Rest API',
        value: 'SweApiStream'
      }, {
        title: 'SOS',
        value: 'SosGetResult'
      }
    ];
    return {dashboardId, clientStore, uiStore, timeSynchronizerIds, state, serverId, types};
  },
  beforeMount() {
    this.callbacks.onSave = this.save;
    this.callbacks.onDelete = this.deleteDataSource;
  },
  methods: {
    save() {
      if (this.edit) {
        console.log('editing dataSource..');
        // check if change serverId
        // if (this.state.server.id !== this.serverId) {
          // move dataSource
        this.uiStore.removeDataSource(this.dashboardId, this.serverId, this.state.dataSource);
        // }
      }
      this.state.dataSource.serverId = this.state.server.id;
      this.uiStore.addOrEditDataSource(this.dashboardId, this.state.server.id, this.state.dataSource);
    },
    async deleteDataSource() {
      console.log('removing ' + this.state.dataSource.id + ', ' + this.state.dataSource.name);
      this.uiStore.removeDataSource(this.dashboardId, this.serverId, this.state.dataSource);
    },
  }
}
</script>
