<template>
  <router-view v-slot="{ Component, route }" >
      <Window
          :is="Component"
          ref="window"
      ></Window>
    <slot v-if="widgetEditorId">
      <DialogForm
          @onClose="widgetEditorId=undefined"
          cssClass="widget-form-content"
          :edit="edit"
          name="widget"
      >
        <template #content="{ save }">
          <WidgetForm :id="widgetEditorId" :edit="edit"></WidgetForm>
        </template>
      </DialogForm>
    </slot>
    <slot v-else-if="dataSourceEditorId">
      <DialogForm
          title="DataSource"
          @onClose="dataSourceEditorId=undefined"
          :edit="edit"
      >
        <template #content="{ save }">
          <DataSourceForm :id="dataSourceEditorId" :server-id="serverId" :edit="edit"/>
        </template>
      </DialogForm>
    </slot>
    <slot v-else-if="serverEditorId">
        <DialogForm
            title="Server"
            @onClose="serverEditorId=undefined"
            :edit="edit"
        >
          <template #content="{ save }">
            <ServerForm :id="serverEditorId"/>
          </template>
        </DialogForm>
    </slot>
  </router-view>
</template>

<script>
import Window from "@/morpheusx/Window.vue";
import WidgetForm from '@/views/forms/widget/WidgetForm.vue';
import DataSourceForm from "@/views/forms/datasource/DataSourceForm";
import ServerForm from "@/views/forms/server/ServerForm";
import DialogForm from "@/views/forms/DialogForm";
import {useRoute} from "vue-router";

export default {
  name: "Content",
  components: {
    DataSourceForm,
    ServerForm,
    DialogForm,
    WidgetForm,
    Window
  },
  setup(props) {
    const route = useRoute();
    const {widgetId, dashboardId, windowId} = route.params;

    return {widgetId, windowId, dashboardId};
  },
  data() {
    return {
      dataSourceEditorId: undefined,
      serverId: undefined,
      serverEditorId: undefined,
      widgetEditorId: undefined,
      edit: false,
      ServerForm: ServerForm
    }
  },
  beforeMount() {
    this.emitter.on("show-datasource-editor", (props) => {
      this.dataSourceEditorId=props.id;
      this.serverId = props.serverId;
      this.edit = props.edit;
    });
    this.emitter.on("show-server-editor", (props) => {
      this.serverEditorId=props.id;
      this.edit = props.edit;
    });
    this.emitter.on("show-widget-editor", (props) => {
      this.widgetEditorId=props.id;
      this.edit = props.edit;
    });
  },
  beforeUnmount() {
    // unsubscribe component events
    this.emitter.off("show-datasource-editor");
    this.emitter.off("show-server-editor");
    this.emitter.off("show-widget-editor");
  },
}
</script>
<style>
.dialog {
  height: 100%;
}

.v-dialog .v-overlay__content {
  max-width: 600px;
}

.content-dialog {
  height: 100%;
  justify-content: center;
}

.v-badge__badge {
 margin-left: 5px;
}
.widget-form-content {
  padding: 0 !important;
}
</style>

