<script>
import {useClientStore} from "@/store/client";
import {randomUUID} from 'osh-js/core/utils/Utils';
import {useRoute} from "vue-router";

export default {
  name: "ServerList",
  data() {
    return {
      selectedServer: undefined
    }
  },
  setup(props) {
    const route = useRoute();
    const clientStore = useClientStore();
    let {dashboardId} = route.params;

    const dashboard = clientStore.getDashboardById(dashboardId);

    return {clientStore, dashboard};
  },
  methods: {
    uuid() {
      return randomUUID();
    },
    select(dashboardId, server) {
      this.emitter.emit('show-server-editor', {id: server.id, edit: true});
    },
    selectDs(dashboard, dataSource, server) {
      this.emitter.emit('show-datasource-editor', {id: dataSource.id, serverId: server.id, edit: true});
    }
  }
}
</script>

