export function mapToDataSources(clientConfig) {
    const datasources = {};

    for(let jsonServerConfig of clientConfig.servers) {
        for (let jsonDataSourceConfig of jsonServerConfig.dataSources) {
            if (jsonDataSourceConfig.type === 'SweApiStream') {
                datasources[jsonDataSourceConfig.id] = mapToDataSourceSweApiResult(jsonDataSourceConfig, jsonServerConfig);
            } else if (jsonDataSourceConfig.type === 'SosGetResult') {
                datasources[jsonDataSourceConfig.id] = mapToDataSourceSosGetResult(jsonDataSourceConfig, jsonServerConfig);
            }
        }
    }
    return datasources;
}

export function mapToDataSource(clientConfig, dataSourceProps) {
    const serverId = dataSourceProps.serverId;
    let jsonServerConfig;
    for(let server of clientConfig.servers) {
        if(server.id === serverId) {
            jsonServerConfig = server;
            break;
        }
    }
    if(!jsonServerConfig) {
        throw Error(`Cannot find Server config with id ${dataSourceProps.serverId}`);
    }
    console.log(dataSourceProps)
    if(dataSourceProps.type === 'SweApiStream') {
        return  mapToDataSourceSweApiResult(dataSourceProps, jsonServerConfig);
    } else if(dataSourceProps.type === 'SosGetResult') {
        return mapToDataSourceSosGetResult(dataSourceProps, jsonServerConfig);
    }
    throw Error(`Not supported DataSource type ${dataSourceProps.type}`);
}

export function mapToDataSourceSosGetResult(jsonDataSourceConfig, jsonServerConfig) {
    return markRaw(new SweApiDatasource(jsonDataSourceConfig.name, {
        ...jsonDataSourceConfig,
        endpointUrl: jsonServerConfig.endpoint,
        tls: jsonServerConfig.tls
    }));
}

export function mapToDataSourceSweApiResult(jsonDataSourceConfig, jsonServerConfig) {
    return markRaw(new SweApiDatasource(jsonDataSourceConfig.name, {
        ...jsonDataSourceConfig,
        endpointUrl: jsonServerConfig.endpoint,
        tls: jsonServerConfig.tls
    }));
}

export function mapToSynchronizers(clientConfig, dataSources = []) {
    const synchronizers = [];

    for(let timeSync of clientConfig.dataSynchronizers) {
       const dsArr = [];

        for(let ds of dataSources) {
            if (ds.properties.synchronizerId === timeSync.id) {
                dsArr.push(ds);
            }
        }
        let currentTimeSync = markRaw(new DataSynchronizer({
            ...timeSync,
            dataSources: dsArr,
            id: timeSync.id
        }));
        synchronizers.push(currentTimeSync)
    }
    return synchronizers;
}

export function mapToView(widgetConfig) {
    const layers = [];

    for(let layer of widgetConfig.layers) {
        layers.push(mapToLayer(layer));
    }

    let viewProperties  = {
        ...widgetConfig.properties,
        container: widgetConfig.id,
        destroyAfterMutation: false,
        layers: layers,
    };

    if(widgetConfig.type === 'LeafletView') {
        return markRaw(new LeafletView(viewProperties));
    } else if(widgetConfig.type === 'CesiumView') {
        return new CesiumView(viewProperties);
    } else if(widgetConfig.type === 'OpenLayerView') {
        return new OpenLayerView(viewProperties);
    } else if(widgetConfig.type === 'MapboxView') {
        mapboxgl.accessToken = 'pk.eyJ1IjoiZ2FrZXdhMzk0MCIsImEiOiJja2I4ZDZkdDAwMzc5MzFwazZubmFhNzVvIn0.i4O5Cls0aaVSVREIzK151w';
        return new MapboxView({
            container: viewProperties.container,
            zoom: 3,
            ...viewProperties
        });
    }else if(widgetConfig.type === 'DeckGlView') {
        return new DeckGlView(viewProperties);
    } else if(widgetConfig.type === 'VideoView') {
        return new VideoView({
            ...viewProperties,
            css: 'video-view'
        });
    } else if(widgetConfig.type === 'ChartView') {
        return new ChartJsView(viewProperties);
    } else if(widgetConfig.type === 'CesiumControlView'){
        return new CesiumControlView(viewProperties);
    } else {
        throw Error(`Unknown view type ${widgetConfig.type}`);
    }
}

export function mapToLayer(layerConfig) {
    if(layerConfig.class === 'PointMarkerLayer') {
        return new PointMarkerLayer(getLayerProps(layerConfig));
    } else if(layerConfig.class === 'VideoDataLayer') {
        return new VideoDataLayer(getLayerProps(layerConfig));
    } else if(layerConfig.class === 'CurveLayer') {
        return new CurveLayer(getLayerProps(layerConfig));
    } else if(layerConfig.class === 'FrustumLayer') {
        return new FrustumLayer(getLayerProps(layerConfig));
    }
    throw Error(`Layer unknown type ${layerConfig.type}`);
}

function getLayerProps(layerConfig) {
    let props = {};
    // check functions as props
    for(let key in layerConfig) {
        let split = key.split('@func');
        if(split.length > 1) {
            // let funcName = split[0];
            // props[funcName] = parseFn(layerConfig[key]);
            props = {
                ...props,
                ...parseFn(layerConfig[key])
            }
        } else {
            props[key] = layerConfig[key];
        }
    }
    return props;
}
