<template>
  <div class="select-container">
    <v-select
        :label="label"
        :items="dataSources"
        item-title="name"
        item-value="id"
        variant="solo"
        v-model="model.dataSourceId"
        density="compact"
    >
    </v-select>
    <v-btn
        class="btn-select-func"
        variant="tonal"
        depressed
        density="compact"
        icon="mdi-dots-horizontal"
        tile
        small
        @click.stop="showFunctionDialog"
        v-if="model.dataSourceId"
        :disabled="!(model.dataSourceId && isValidMapping())"
    >
    </v-btn>
    <SaveDialog
        :title="dialogFuncTitle"
        :name="funcName"
        cssClass="func-content"
        v-if="showFuncDialog"
        @onClose="onClose"
        @onSave="save"
    >
      <template #content>
        <v-textarea v-model="model[funcName]" auto-grow :label="dialogFuncTitle" rows="20" density="comfortable">
        </v-textarea>
      </template>
    </SaveDialog>
  </div>
  <template v-for="(value,key) in model">
    <v-combobox
        :items="mapping.value"
        :label="key"
        variant="solo"
        density="compact"
        v-model="model[key]"
        item-title="uiLabel"
        return-object
        v-if="key !== 'dataSourceId' && !key.startsWith('get')"
    >
      <template v-slot:selection="{ item }">
        <v-tooltip location="start">
          <template v-slot:activator="{ props }">
            <v-icon v-bind="props" color="grey-lighten-1" class="info-icon">
              mdi-information
            </v-icon>
          </template>
          <span>
            <v-col cols="12" class="flex" justify="space-between">
              <template v-for="(item, key) in item.value.tooltip">
                <v-row><b>{{ key }}</b>:
                  <template v-if="key === 'definition'"><a :href="item" target="_blank"
                                                           rel="noopener noreferrer"> {{ item }} </a></template>
                  <template v-else> {{ item }}</template>
                </v-row>
              </template>
            </v-col>
          </span>
        </v-tooltip>
        {{ item.title }}
      </template>
      <template v-slot:item="data">
        <v-list-item v-bind="data.props">
          <template v-slot:title="{ title }">
            <v-tooltip location="start">
              <template v-slot:activator="{ props }">
                <v-icon v-bind="props" color="grey-lighten-1" class="info-icon">
                  mdi-information
                </v-icon>
              </template>
              <span>
            <v-col cols="12" class="flex" justify="space-between">
              <template v-for="(item, key) in data.item.value.tooltip">
                <v-row><b>{{ key }}</b>:
                  <template v-if="key === 'definition'"><a :href="item" target="_blank"
                                                           rel="noopener noreferrer"> {{ item }} </a></template>
                  <template v-else> {{ item }}</template>
                </v-row>
              </template>
            </v-col>
          </span>
            </v-tooltip>
            {{ title }}
          </template>
        </v-list-item>
      </template>
    </v-combobox>
  </template>
</template>

<script>
import SaveDialog from "@/components/SaveDialog";

export default {
  name: "DataSourceMapping",
  props: ['label','name','dataSources', 'mapping','model','transformFunc'],
  components: {
    SaveDialog,
  },
  setup(props) {
    const dialogFuncTitle = `Function get${props.name}():`;
    const funcName = `get${props.name}`;
    return {
      dialogFuncTitle, funcName
    }
  },
  data() {
    return {
      showFuncDialog: false
    }
  },
  methods: {
    onClose() {
      this.showFuncDialog = false;
    },
    onSave() {
      this.showFuncDialog = false;
    },
    isValidMapping() {
      for(let prop in this.model) {
        if(!this.model[prop]) {
          return false;
        }
      }
      return true;
    },
    showFunctionDialog() {
      if(!this.model[this.funcName]) {
        // inject custom content
        this.model[this.funcName] = this.transformFunc(this.model);
      }
      this.showFuncDialog = true;
    },
    save() {
      this.showFuncDialog = false;
    }
  }
}
</script>

<style scoped>
.select-container {
  display: flex;
}
.btn-select-func {
  align-self: center;
  margin: 5px;
}
</style>
<style>
.func-content {
  height: 100%;
  width: 100%;
}
</style>
