import {defineStore} from "pinia";
import {toRaw} from "vue";
import {mapToDataSource, mapToDataSources, mapToSynchronizers} from "@/morpheusx/factory";
import {DEFAULT_CONFIG} from "./defaultConfig";
import {isDefined, randomUUID, merge} from 'osh-js/core/utils/Utils';
import {toArray} from "@/utils";
import {useSweStore} from "@/store/swe";

let sweStore;

const DASHBOARD_API = {
    user: 'webadmin',
    password: '6uw8vJ9awW+%7yM!',
    url: 'https://api.georobotix.io/ogc/t18/admin-api/dashboards'
};

function getSweStore() {
    if(!isDefined(sweStore)) {
        sweStore = useSweStore();
    }
    return sweStore;
}

let timeSynchronizers = {};

export const useClientStore = defineStore('clientStore', {
    state: () => {
        return {
            id: randomUUID(),
            clientConfig: {},
            initialized: false,
            syncVersion: 0
        }
    },
    getters: {},
    actions: {
        async getDashboardByIdFromRemote(dashboardId, isPopup) {
            await this.checkConf(dashboardId,isPopup);
            return this.clientConfig;
        },

        saveConf(dashboardId) {
            const headers = {};

            headers['Accept'] = '*/*';
            headers['Accept-Encoding'] = 'gzip, deflate, br';
            headers['Content-Type'] = 'application/json';
            headers['Authorization'] = 'Basic ' + window.btoa(`${DASHBOARD_API.user}:${DASHBOARD_API.password}`);

            fetch(`${DASHBOARD_API.url}/${dashboardId}`, {
                method: 'PUT',
                headers: headers,
                credentials: 'include',
                body: JSON.stringify(this.clientConfig)
            }).catch(error => {
                console.error('There was an error!', error);
            });
        },

        getDashboardById(dashboardId) {
            return this.clientConfig;
        },
        connect(dashboardId) {
            if (dashboardId in timeSynchronizers) {
                for (let t of timeSynchronizers) {
                    t.connect();
                }
            }
        },
        disconnect(dashboardId) {
            if (dashboardId in timeSynchronizers) {
                for (let t of timeSynchronizers) {
                    t.disconnect();
                }
            }
        },
        loadConf( clientConfig, isPopup=false) {
            //
            try {
                if(!isPopup && ('servers' in clientConfig)) {
                    this.addTimeSynchronizers(clientConfig.id);
                }
            } catch (ex) {
                throw ex;
            }
        },
        addServer(dashboardId, props) {
            this.clientConfig.servers.push(props);
            this.updateVersion();
        },
        removeServer(dashboardId, serverId) {
            this.clientConfig.servers = this.clientConfig.servers.filter(server => server.id !== serverId);
            this.updateVersion();
        },
        editServer(serverId, props) {
            let idx = 0;
            for (let server of this.clientConfig.servers) {
                if (server.id === serverId) {
                    this.clientConfig.servers[idx] = {
                        ...server,
                        ...props
                    };
                }
                idx++;
            }
            this.updateVersion();
        },
        getServersByDashboard(dashboardId) {
            return this.clientConfig.servers;
        },
        findServerById(dashboardId , serverId) {
            for (let server of this.clientConfig.servers) {
                if (server.id === serverId) {
                    return server;
                }
            }
            return undefined;
        },
        addTimeSynchronizers(dashboardId) {
            timeSynchronizers = [];
            const dataSourcesArr = [];
            const dataSources = mapToDataSources(toRaw(this.clientConfig));
            for (let dsId in dataSources) {
                // dataSources[ds].subscribe(message => console.log(message), [EventType.DATA]);
                dataSourcesArr.push(dataSources[dsId]);
            }

            timeSynchronizers = mapToSynchronizers(toRaw(this.clientConfig), dataSourcesArr);
            for (let dsId in dataSources) {
                getSweStore().setActiveUpdateDataSource(dashboardId, dsId, timeSynchronizers[0]);
            }
        },

        getTimeSynchronizerIds() {
            const ids = [];
            for (let timeSynchronizer of timeSynchronizers) {
                ids.push(timeSynchronizer.id);
            }
            return ids;
        },
        createNewDashboard(id, name='') {
            const defaultDashboard = DEFAULT_CONFIG(id, name);
            this.clientConfig = defaultDashboard;

            //
            this.loadConf(this.clientConfig);
            this.updateVersion();
            return defaultDashboard;
        },
        addWindow(dashboardId, window) {
            this.clientConfig.windows[window.id] = window;
            this.updateVersion();
        },
        editWindow(dashboardId, windowId, props) {
            this.clientConfig.windows[windowId] = {
                ...this.clientConfig.windows[windowId],
                ...props
            };
            this.updateVersion();
        },
        removeWindowById(dashboardId, windowId, syncVersion = true) {
            delete this.clientConfig.windows[windowId];
            if (syncVersion) {
                this.updateVersion();
            }
        },
        getWindowById(dashboardId, id) {
            return (id in this.clientConfig.windows) ? this.clientConfig.windows[id] : undefined;
        },
        getWidgetById(dashboardId, windowId, widgetId) {
            return (windowId in this.clientConfig.windows && widgetId in this.clientConfig.windows[windowId].widgets) ?
                this.clientConfig.windows[windowId].widgets[widgetId] : undefined;
        },
        removeWidget(dashboardId, windowId, widgetId) {
            delete this.clientConfig.windows[windowId].widgets[widgetId];
            this.updateVersion();
        },
        removeAllWidgets(dashboardId, windowId, syncVersion = true) {
            this.clientConfig.windows[windowId].widgets = {};
            if (syncVersion) {
                this.updateVersion();
            }
        },
        async loadFromServer(dashboardId) {
            const headers = {};

            headers['Accept'] = '*/*';
            headers['Accept-Encoding'] = 'gzip, deflate, br';
            headers['Content-Type'] = 'application/json';
            headers['Authorization'] = 'Basic ' + btoa(`${DASHBOARD_API.user}:${DASHBOARD_API.password}`);

            this.clientConfig = await fetch(`${DASHBOARD_API.url}/${dashboardId}`,{
                method: 'GET',
                headers: headers,
                credentials: 'include',
            }).then(response => {
                return response.json();
            }).catch(error => {
                console.error('There was an error!', error);
            });
        },

        async checkConf(dashboardId,isPopup) {
            if (!this.initialized) {
                await this.loadFromServer(dashboardId);
                this.loadConf(this.clientConfig, isPopup);
                console.log(this.clientConfig)
                this.initialized = true;
            }
        },
        getWindowsByType(dashboardId, type) {
            return toArray(this.clientConfig.windows).filter(el => el.type === type);
        },
        setWidget(dashboardId, window, widget) {
            if (!(window.id in this.clientConfig.windows)) {
                this.clientConfig.windows[window.id] = window;
            }
            this.clientConfig.windows[window.id].widgets[widget.id] = widget;
            this.updateVersion();
        },
        getWidgets(dashboardId, windowId) {
            return this.clientConfig.windows[windowId].widgets;
        },
        //datasources
        findDataSourceById(dashboardId, dataSourceId) {
            for (let server of this.clientConfig.servers) {
                for (let datasource of server.dataSources) {
                    if (datasource.id === dataSourceId) {
                        datasource.serverId = server.id; // inject serverId for faster find
                        return datasource;
                    }
                }
            }
            return undefined;
        },
        addOrEditDataSource(dashboardId, serverId, props,isConnected) {
            const dataSynchronizer = this.getDefaultTimeSynchronizer(dashboardId);
            //TODO:support multi synchronizers
            props.synchronizerId = this.clientConfig.dataSynchronizers[0].id;

            for(let i=0;i < this.clientConfig.servers.length; i++) {
                let server = this.clientConfig.servers[i];
                if (server.id === serverId) {
                    // check if exists
                    let addNew = true;
                    let idx = 0;
                    for(let dataSource of server.dataSources) {
                        if(dataSource.id === props.id) {
                            // replace content
                            this.clientConfig.servers[i].dataSources[idx] = {
                                ...dataSource,
                                ...props
                            };
                            addNew = false;
                            break;
                        }
                        idx++;
                    }
                    if(addNew) {
                        server.dataSources.push(props);
                    }
                    break;
                }
            }
            const dataSource = mapToDataSource(toRaw(this.clientConfig),toRaw(props));
            dataSynchronizer.addDataSource(dataSource);
        },
        async removeDataSource(dashboardId, serverId, props) {
            for (let i = 0; i < this.clientConfig.servers.length; i++) {
                let server = this.clientConfig.servers[i];
                if (server.id === serverId) {
                    this.clientConfig.servers[i].dataSources =
                        this.clientConfig.servers[i].dataSources.filter(ds => ds.id !== props.id);
                    break;
                }
            }
            const dataSynchronizer = this.getDefaultTimeSynchronizer(dashboardId);
            // find datasource from dataSynchronizer properties
            const dsArr = dataSynchronizer.getDataSources();
            for (let datasource of dsArr) {
                if (datasource.id === props.id) {
                    await dataSynchronizer.removeDataSource(datasource);
                    break;
                }
            }
            this.updateVersion();
        },
        reset(dashboardId) {
            for (let windowKey in this.clientConfig.windows) {
                // remove popups
                let window = this.clientConfig.windows[windowKey];
                if (window.type === 'popup') {
                    delete this.clientConfig.windows[windowKey];
                } else {
                    window.widgets = {};
                }
            }
        },
        getDefaultTimeSynchronizer(dashboardId) {
            return timeSynchronizers[0];
        },
        updateVersion() {
            this.syncVersion++;
        },
    },
})

