<template>
  <v-select
      label="Modes"
      :items="modes"
      variant="solo"
      density="compact"
      v-model="state.dataSource.mode"
  ></v-select>
  <template v-if="state.dataSource.mode === 'replay'">
  <v-text-field variant="solo" density="compact" label="Time shift" v-model="state.dataSource.timeShift" ></v-text-field>
  <!---v-text-field variant="solo" density="compact" label="Start Time" v-model="state.dataSource.startTime" ></v-text-field-->
  <!--v-text-field variant="solo" density="compact" label="End Time" v-model="state.dataSource.endTime" ></v-text-field-->
  <v-text-field variant="solo" density="compact" label="Prefetch batch duration" v-model="state.dataSource.prefetchBatchDuration" ></v-text-field>
  <v-text-field variant="solo" density="compact" label="Prefetch batch size" v-model="state.dataSource.prefetchBatchSize" ></v-text-field>
  </template>
</template>

<script>
import {inject} from 'vue';

export default {
  name: "ModeForm",
  setup(props) {
    const modes = [
      { title: 'Real Time', value: 'realTime'},
      { title: 'Replay', value: 'replay' },
      { title: 'Batch', value: 'batch'}
    ];
    const state = inject('state');

    return {modes, state};
  },
  data() {
    return {
      mode: 'realTime',
      realTime: {
        startTime: 'now', // not mutated
        endTime: '2055-01-01T00:00:00Z', // not mutated
        timeShift: 0, // not mutated
        prefetchBatchDuration: 5000, // not mutated
        prefetchBatchSize: 250 // not mutated
      },
      replay: {
        startTime: '2000-01-01T00:00:00Z',
        endTime: '2055-01-01T00:00:00Z',
        timeShift: 0,
        prefetchBatchDuration: 5000,
        prefetchBatchSize: 250
      }
    }
  },
}
</script>

