<template>
  <v-dialog
      v-model="showDialog"
      :close-on-content-click="false"
      :nudge-right="40"
      transition="scale-transition"
      offset-y
      max-width="700px"
      max-height="850px"
  >
    <template v-slot:activator="{ props }">
      <v-text-field
          v-model="selectedIcon"
          :prepend-inner-icon='"mdi-"+selectedIcon'
          :label="label"
          v-bind="props"
          readonly
          variant="solo"
          density="compact"
          class="input-text"
      >
      </v-text-field>
    </template>
    <v-card>
      <v-container>
        <v-text-field
            v-model="searchIcon"
            placeholder="Search icon"
            prepend-icon="mdi-magnify"
            type="text"
            onautocomplete="off"
            density="compact"
        />
        <v-sheet
            id="scrolling-techniques-7"
            class="overflow-y-auto"
            max-height="600"
            v-scroll.self="onScroll"
        >
          <v-col cols="12">
            <div id="container" ref="container">
              <template
                  v-for="(item) in itemsToDisplay"
                  :key="item"
              >
<!--                  <v-tooltip bottom >-->
<!--                    <template v-slot:activator="{ props }" >-->
                      <v-btn
                          :title="item"
                          @click="saveIcon(item)"
                          color="white"
                          class="mr-2 mb-2 "
                          fab
                          small
                          depressed
                      >
                        <v-icon> mdi-{{ item }}</v-icon>
                      </v-btn>
<!--                    </template>-->
<!--                    <span v-if="!this.loading">{{ item }}</span>-->
<!--                  </v-tooltip>-->
              </template>

              <template v-if="loading">
                <div id="loading-wrapper">
                  <div class="dots">
                    <div :style="`background-color: #18191A`"></div>
                    <div :style="`background-color: #18191A`"></div>
                    <div :style="`background-color: #18191A`"></div>
                  </div>
                </div>
              </template>

              <div v-show="((page !== items.length - 1) || !loading)" id="end-of-list" ref="end-of-list"/>

            </div>
          </v-col>
        </v-sheet>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>

import {mdiIcons} from "./mdi-icons";
import {RadixTree} from './radix_tree';
import {chunkArray} from "@/utils";
import {throttle, debounce} from "osh-js/core/utils/Utils";

export default {
  name: "MdiIconPicker",
  props: {
    defaultIcon: {
      type: String,
      default: () => 'map-marker'
    },
    label: {
      type: String,
      default: () => 'Color'
    }
  },
  data() {
    return {
      loading: false,
      showDialog: false,
      searchIcon: '',
      selectedIcon: this.defaultIcon,
      radix: new RadixTree(mdiIcons),
      items: [],
      page: 0, // page represents the index of last small array in the list
      itemsToDisplay: [], // the list of items to be rendered
      itemsPerRender: 100,
      minLoadingDuration: 50,
      debouncedWatch: debounce((newValue, oldValue,cb) => {
        cb(newValue);
      }, 255)
    }
  },
  created(){
    this.updateList();
    this.$watch('data', function () {
      this.updateList();
    }, {deep:true})
  },
  mounted(){
    this.loadItems()
  },
  methods: {
    onScroll() {
      this.loadItems();
    },
    saveIcon(icon) {
      this.selectedIcon = icon;
      this.showDialog = false;
      this.$emit('onSelect', this.selectedIcon);
    },
    // set the list and update it when data changes
    updateList(contentArr = mdiIcons) {
      const chunckedArray = chunkArray(contentArr, this.itemsPerRender); // chunkArray(data,itemsPerRender) to get array of small arrays
      this.items = chunckedArray;
      this.itemsToDisplay = chunckedArray[0];
    },

    // load more items when scrolling to the end of the list
    loadItems(){
      if(this.page === this.items.length - 1) return

      const element = this.$refs["end-of-list"] //this.endOfList;
      if(!element) return

      const position = element.getBoundingClientRect();
      // checking whether fully visible
      if((position.top >= 0 && position.bottom <= window.innerHeight  ) && !this.loading) {
        this.loading = true
        this.page++
        const st = performance.now();
        const results = [...this.itemsToDisplay, ...this.items[this.page]];
        const end = performance.now();
        const duration = end - st;

        const timeoutDuration = ((end-st) > this.minLoadingDuration) ? 0: this.minLoadingDuration - duration;
        setTimeout(() => {
          this.itemsToDisplay = results;
          this.loading = false
          this.loadItems()
        }, timeoutDuration);
      }
    },
  },
  watch: {
    searchIcon(newSearchIcon, oldSearchIcon) {
      this.loading = true;
      this.debouncedWatch(newSearchIcon, oldSearchIcon, (newSearchIcon) => {
        this.page = 0;
        this.updateList(this.radix.getSyncWords(newSearchIcon));
        this.loading = false;
      });
    }
  }
}
</script>

<style scoped>
i.v-icon.v-icon {
  color: grey !important;
}
.hidden{
  display: none;
}

/* List container style */
#container{
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scrollbar-width: thin;
}
#end-of-list{
  height: 32px;
  width: 100%;
}
#loading-wrapper{
  width: 100%;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Loading style */
.dots {
  width: 3.5em;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}
.dots div {
  width: 0.8em;
  height: 0.8em;
  border-radius: 50%;
  animation: fade 0.8s ease-in-out alternate infinite;
}
.dots div:nth-of-type(1) {
  animation-delay: -0.4s;
}
.dots div:nth-of-type(2) {
  animation-delay: -0.2s;
}
@keyframes fade {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@media(max-width: 480px) {
  .dots div {
    width: 0.7em;
    height: 0.7em;
  }
}

</style>
