<template>
  <v-card :elevation="4" :width="width" :height="height" color="#96a2ab52" class="card">
    <v-card-item class="card-item">
      <div class="action-button">
        <v-btn
            class="delete-view-button"
            :rounded="0"
            x-small
            @click.stop="deleteView"
            color="red"
            density="compact"
            flat
            v-if="this.uiStore.ui.edit"
        >
          <v-icon icon="mdi-close" size="large"></v-icon>
        </v-btn>
        <v-btn
            class="edit-view-button"
            :rounded="0"
            x-small
            @click.stop="editView"
            density="compact"
            flat
            v-if="this.uiStore.ui.edit"
        >
          <v-icon icon="mdi-pencil" size="large"></v-icon>
        </v-btn>
        <v-tooltip v-if="widget && widget.name && !timeController ">
          <template v-slot:activator="{ props }">
            <v-icon v-bind="props" color="grey-lighten-1" class="info-icon">
              mdi-information
            </v-icon>
          </template>
          {{ widget.name }}
        </v-tooltip>
      </div>
      <v-card-text>
        <TimeController
            :dataSynchronizer="dataSynchronizer"
            :skipTimeStep="'10%'"
            :onControlEventFn='onControlEvent'
            :trackRealtime="trackRealtime"
            v-if="timeController"
        ></TimeController>
        <div :id="widgetId" class="widget" v-else></div>
      </v-card-text>
    </v-card-item>
  </v-card>
  <ConfirmDialog ref="confirm"/>
</template>

<script>
import {useClientStore} from "@/store/client";
import {useUiStore} from "@/store/ui";
import {mapToView} from "./factory";
import ConfirmDialog from "@/components/ConfirmDialog.vue";
import {toRaw, inject} from "vue";
import {isDefined} from 'osh-js/core/utils/Utils';
import {useRoute} from "vue-router";
import TimeController from 'osh-js/vue/components/TimeController.vue';

export default {
  name: "Widget",
  components: {
    ConfirmDialog, TimeController
  },
  inject: ["$item"],
  props: ['id', 'bk'],
  emits: ['moveEnd', 'resizeEnd'],
  setup(props) {
    const windowId = inject('windowId');
    let widgetId = props.id;
    const uiStore = useUiStore();
    const clientStore = useClientStore();
    const route = useRoute();

    const {dashboardId} = route.params;
    return {widgetId, dashboardId, uiStore, clientStore, windowId};
  },
  data() {
    return {
      widget: undefined,
      view: undefined,
      trackRealtime: '',
      subscriptions: [],
      lastPosition: {
        x: undefined,
        y: undefined
      },
      timeController: false,
      versionTimeController: 0
    }
  },
  mounted() {
    try {
      this.widget = this.clientStore.getWidgetById(this.dashboardId, this.windowId, this.widgetId);
      if (isDefined(this.widget)) {
        if (this.widget.uiType === 'Timebar') {
          this.timeController = true;
        } else {
          // create OSH views
          // They are automatically bound by their ids
          this.view = mapToView(toRaw(this.widget));
        }
      } else {
        console.warn('widget not defined!')
      }

      const unsubscribe = this.uiStore.$onAction(({name, store, args, after, onError}) => {
            if (name === 'hookReloadWidget') {
              after((props) => {
                if ((props.name === 'addOrEditDataSource' || props.name === 'removeDataSource') && this.timeController) {
                  // this.versionTimeController++;
                } else  if(props.name === 'reset-from-timebar') {
                  this.widget = props.widget;
                  if (this.widget.id === this.widgetId && this.view) {
                    //TODO: use reset() instead => currently launch exception
                    this.view.destroy();
                    this.view = mapToView(toRaw(this.widget));
                  }
                } else {
                  this.widget = props.widget;
                  if (this.widget.id === this.widgetId) {
                    this.view.destroy();
                    this.view = mapToView(toRaw(this.widget));
                  }
                }
              });
            }
          }
      );
      this.subscriptions.push(unsubscribe);
    } catch (ex) {
      console.error(ex);
    }
  },
  beforeUnmount() {
    if (isDefined(this.view)) {
      this.view.destroy();
    }
    // release action subscriptions
    for (let unsubscribe of this.subscriptions) {
      unsubscribe();
    }
  },
  watch: {
    // this happens when an item is set at the first positon {0; 0} and move an existing one.
    // In that case, we have to catch the move event from the existing one which is moved by vue dash responsive lib
    x() {
      if (this.lastPosition.x !== this.item.x || this.lastPosition.y !== this.item.y) {
        this.lastPosition.x = this.item.x;
        this.lastPosition.y = this.item.y;
        this.$emit('moveEnd', this.item);
      }
    },
    y() {
      if (this.lastPosition.x !== this.item.x || this.lastPosition.y !== this.item.y) {
        this.lastPosition.x = this.item.x;
        this.lastPosition.y = this.item.y;
        this.$emit('moveEnd', this.item);
      }
    },
  },
  computed: {
    dataSynchronizer() {
      return this.clientStore.getDefaultTimeSynchronizer(this.dashboardId);
    },
    show() {
      return this.uiStore.ui.connected;
    },
    item() {
      return this.$item();
    },
    x() {
      if (this.item) {
        return this.item.x;
      }
      return 0;
    },
    y() {
      if (this.item) {
        return this.item.y;
      }
    },
    width() {
      if (this.item) {
        return this.item.widthPx;
      }
      return 0;
    },
    height() {
      if (this.item) {
        return this.item.heightPx;
      }
      return 0;
    },
  },
  methods: {
    editView(event) {
      this.emitter.emit('show-widget-editor', {id: this.widgetId, edit: true});
    },
    onControlEvent(eventName) {
      if ((eventName === 'forward' || eventName === 'backward' || eventName === 'end'
            || eventName === 'replaySpeed'
            || (eventName === 'play'))) {
          this.uiStore.resetFromTimebar(this.dashboardId, this.windowId, {});
        }
    },
    async deleteView(event) {
      if (
          await this.$refs.confirm.open(
              "Confirm",
              "Are you sure you want to remove this view?"
          )
      ) {
        console.log('removing ' + this.widgetId + 'from ' + this.windowId);
        this.uiStore.removeGridItem(this.dashboardId, this.windowId, this.widgetId);
      }
    }
  }
};
</script>

