<template>
  <!-- Origin -->
  <DataSourceMapping
      label="Origin DataSource"
      name="Origin"
      :model="layer.ui.origin"
      :mapping="originDataSourceMapping"
      :dataSources="dataSources"
      :transformFunc="originTransformFunc"
  ></DataSourceMapping>

  <!-- Platform Orientation -->
  <DataSourceMapping
      label="Platform Orientation DataSource"
      name="PlatformOrientation"
      :model="layer.ui.platformOrientation"
      :mapping="platformOrientationDataSourceMapping"
      :dataSources="dataSources"
      :transformFunc="platformOrientationTransformFunc"
  ></DataSourceMapping>

  <!-- Sensor Orientation-->
  <DataSourceMapping
      label="Sensor Orientation DataSource"
      name="SensorOrientation"
      :model="layer.ui.sensorOrientation"
      :mapping="sensorOrientationDataSourceMapping"
      :dataSources="dataSources"
      :transformFunc="sensorOrientationTransformFunc"
  ></DataSourceMapping>

  <!-- Range-->
  <DataSourceMapping
      label="Range DataSource"
      name="Range"
      :model="layer.ui.range"
      :mapping="rangeDataSourceMapping"
      :dataSources="dataSources"
      :transformFunc="rangeTransformFunc"
  ></DataSourceMapping>

  <!-- FoV-->
  <DataSourceMapping
      label="FoV DataSource"
      name="Fov"
      :model="layer.ui.fov"
      :mapping="fovDataSourceMapping"
      :dataSources="dataSources"
      :transformFunc="fovTransformFunc"
  ></DataSourceMapping>

  <ColorPicker v-model="layer.color"  label="Color"></ColorPicker>
  <v-slider
      v-model="layer.opacity"
      :max="1.0"
      min="0"
      :step="0.1"
      show-ticks="always"
      label="Opacity"
      hide-details
      class="ma-0 opacity-slider"
      density="compact"
      variant="solo"
  >
    <template v-slot:append>
      <v-text-field
          v-model="layer.opacity"
          type="number"
          style="width: 85px"
          density="compact"
          hide-details
          variant="outlined"
      ></v-text-field>
    </template>
  </v-slider>
</template>
<script>

import {inject, reactive} from "vue";
import {isDefined} from 'osh-js/core/utils/Utils';
import {useSweStore} from "@/store/swe";
import {useRoute} from "vue-router/dist/vue-router";
import {stringifyFn} from "@/utils";
import ColorPicker from "@/components/ColorPicker.vue";
import DataSourceMapping from "@/views/forms/widget/DataSourceMapping";

function defaultCesiumFrustumLayer() {
  return {
    color: '#B0B0B0B3',
    opacity: 0.25,
    class: 'FrustumLayer',
  }
}

export default {
  name: "WidgetFormFrustumLayer",
  components: {
    ColorPicker,
    DataSourceMapping
  },
  setup(props) {
    const sweStore = useSweStore();
    const route = useRoute();
    const {dashboardId } = route.params;
    const dataSources = inject('dataSources');

    let layer = inject('reactiveLayer');
    const widget = inject('widget');
    if(!layer.class) {
      if (widget.name.toLowerCase() === 'cesium') {
        Object.assign(layer, defaultCesiumFrustumLayer());
      } else {
        throw Error(`View ${widget.name} does not support Frustum Layer`);
      }
    }
    if(!layer.ui.origin) {
      layer.ui.origin = {
        x: undefined,
        y: undefined,
        z: undefined,
        dataSourceId: undefined
      };
      layer.ui.range = {
        dataSourceId: undefined,
        data: undefined
      };
      layer.ui.fov = {
        dataSourceId: undefined,
        data: undefined
      };
      layer.ui.platformOrientation = {
        dataSourceId: undefined,
        pitch: undefined,
        heading: undefined,
        roll: undefined
      };
      layer.ui.sensorOrientation = {
        dataSourceId: undefined,
        pitch: undefined,
        yaw: undefined,
        roll: undefined
      };
    }

    return {layer, sweStore, dataSources, dashboardId };
  },
  computed: {
    originDataSourceMapping() {
      return this.getMapping(this.layer.ui.origin.dataSourceId);
    },
    platformOrientationDataSourceMapping() {
      return this.getMapping(this.layer.ui.platformOrientation.dataSourceId);
    },
    sensorOrientationDataSourceMapping() {
      return this.getMapping(this.layer.ui.sensorOrientation.dataSourceId);
    },
    fovDataSourceMapping() {
      return this.getMapping(this.layer.ui.fov.dataSourceId);
    },
    rangeDataSourceMapping() {
      return this.getMapping(this.layer.ui.range.dataSourceId);
    },
  },
  watch: {
    'layer.ui.origin': {
      handler(val) {
        this.updateFunc();
      }, deep: true
    },
    'originDataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.origin = {
            dataSourceId: undefined,
            x: undefined,
            y: undefined,
            z: undefined
          }
        }
      }
    },
    'layer.ui.platformOrientation': {
      handler(val) {
        this.updateFunc();
      }, deep: true
    },

    'platformOrientationDataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.platformOrientation = {
            dataSourceId:undefined,
            heading: undefined,
            pitch: undefined,
            roll: undefined
          };
        }
      }
    },
    'layer.ui.sensorOrientation': {
      handler(val) {
        this.updateFunc();
      }, deep: true
    },
    'sensorOrientationDataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.sensorOrientation = {
            dataSourceId: undefined,
            yaw: undefined,
            pitch : undefined,
            roll: undefined
          };
        }
      }
    },
    'layer.ui.fov': {
      handler(val) {
        this.updateFunc();
      }, deep: true
    },
    'fovDataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.fov = {
            dataSourceId: undefined,
            data: undefined
          };
        }
      }
    },
    'layer.ui.range': {
      handler(val) {
        this.updateFunc();
      }, deep: true
    },
    'rangeDataSourceMapping.value': {
      handler (val, oldVal) {
        if(isDefined(oldVal) && Object.keys(oldVal).length > 0) {
          this.layer.ui.range = {
            dataSourceId: undefined,
            data: undefined
          };
        }
      }
    },
  },
  methods: {
    updateFunc() {
      if(this.layer.ui.origin.x &&
          this.layer.ui.origin.y &&
          this.layer.ui.origin.z &&
          this.layer.ui.platformOrientation.heading &&
          this.layer.ui.platformOrientation.pitch &&
          this.layer.ui.platformOrientation.roll &&
          this.layer.ui.sensorOrientation.yaw &&
          this.layer.ui.sensorOrientation.pitch &&
          this.layer.ui.sensorOrientation.roll &&
          this.layer.ui.fov.data &&
          this.layer.ui.range.data
      ) {

        let originFnAsStr, platformOrientationFnAsStr, sensorOrientationFnAsStr, rangeFnAsStr, fovFnAsStr;
        if(this.layer.ui.origin.getOrigin) {
          originFnAsStr = this.layer.ui.origin.getOrigin;
        } else {
          // compute one
          originFnAsStr = this.originTransformFunc(this.layer.ui.origin);
        }

        if(this.layer.ui.platformOrientation.getPlatformOrientation) {
          platformOrientationFnAsStr = this.layer.ui.platformOrientation.getPlatformOrientation;
        } else {
          // compute one
          platformOrientationFnAsStr = this.platformOrientationTransformFunc(this.layer.ui.platformOrientation);
        }


        if(this.layer.ui.sensorOrientation.getSensorOrientation) {
          sensorOrientationFnAsStr = this.layer.ui.sensorOrientation.getSensorOrientation;
        } else {
          // compute one
          sensorOrientationFnAsStr = this.sensorOrientationTransformFunc(this.layer.ui.sensorOrientation);
        }

        if(this.layer.ui.range.getRange) {
          rangeFnAsStr = this.layer.ui.range.getRange;
        } else {
          // compute one
          rangeFnAsStr = this.rangeTransformFunc(this.layer.ui.range);
        }

        if(this.layer.ui.fov.getFov) {
          fovFnAsStr = this.layer.ui.fov.getFov;
        } else {
          // compute one
          fovFnAsStr = this.fovTransformFunc(this.layer.ui.fov);
        }

        let funcStr = originFnAsStr;
        funcStr = funcStr.slice(0, -2) + ',' + platformOrientationFnAsStr.slice(1);
        funcStr = funcStr.slice(0, -2) + ',' + sensorOrientationFnAsStr.slice(1);
        funcStr = funcStr.slice(0, -2) + ',' + rangeFnAsStr.slice(1);
        funcStr = funcStr.slice(0, -2) + ',' + fovFnAsStr.slice(1);

        this.layer['frustum@func'] = funcStr;
      }
    },

    originTransformFunc(model) {
      let originXPath = (typeof model.x === 'object') ? `rec${model.x.data.path.replaceAll('\/', '.')}` : `${model.x}`;
      let originYPath = (typeof model.y === 'object') ? `rec${model.y.data.path.replaceAll('\/', '.')}` : `${model.y}`;
      let originZPath = (typeof model.z === 'object') ? `rec${model.z.data.path.replaceAll('\/', '.')}` : `${model.z}`;

      const func = {
        getOrigin: {
          dataSourceIds: [model.dataSourceId],
          handler: function (rec) {
            return {
              x: $originXPath$,
              y: $originYPath$,
              z: $originZPath$
            };
          }
        }
      }
      let fnAsStr = stringifyFn(func);

      fnAsStr = fnAsStr.replace('$originXPath$', originXPath);
      fnAsStr = fnAsStr.replace('$originYPath$', originYPath);
      fnAsStr = fnAsStr.replace('$originZPath$', originZPath);

      return fnAsStr;

    },
    platformOrientationTransformFunc(model) {
      let platformOrientationHeadingPath = (typeof model.heading === 'object') ? `rec${model.heading.data.path.replaceAll('\/', '.')}` : `${model.heading}`;
      let platformOrientationPitchPath = (typeof model.pitch === 'object') ? `rec${model.pitch.data.path.replaceAll('\/', '.')}` : `${model.pitch}`;
      let platformOrientationRollPath = (typeof model.roll === 'object') ? `rec${model.roll.data.path.replaceAll('\/', '.')}` : `${model.roll}`;

      const func = {
        getPlatformOrientation: {
          dataSourceIds: [model.dataSourceId],
          handler: function(rec) {
            return  {
              heading: $platformOrientationHeading$,
              pitch: $platformOrientationPitch$,
              roll: $platformOrientationRoll$
            };
          }
        }
      };
      let fnAsStr = stringifyFn(func);

      fnAsStr = fnAsStr.replace('$platformOrientationHeading$', platformOrientationHeadingPath);
      fnAsStr = fnAsStr.replace('$platformOrientationPitch$', platformOrientationPitchPath);
      fnAsStr = fnAsStr.replace('$platformOrientationRoll$', platformOrientationRollPath);

      return fnAsStr;
    },
    sensorOrientationTransformFunc(model) {
      let sensorOrientationYawPath = (typeof model.yaw === 'object') ? `rec${model.yaw.data.path.replaceAll('\/', '.')}` : `${model.yaw}`;
      let sensorOrientationPitchPath = (typeof model.pitch === 'object') ? `rec${model.pitch.data.path.replaceAll('\/', '.')}` : `${model.pitch}`;
      let sensorOrientationRollPath = (typeof model.roll === 'object') ? `rec${model.roll.data.path.replaceAll('\/', '.')}` : `${model.roll}`;

      const func = {
        getSensorOrientation: {
          dataSourceIds: [model.dataSourceId],
          handler: function(rec) {
            return {
              yaw: $sensorOrientationYaw$,
              pitch: $sensorOrientationPitch$,
              roll: $sensorOrientationRoll$
            };
          }
        }
      };
      let fnAsStr = stringifyFn(func);
      fnAsStr = fnAsStr.replace('$sensorOrientationYaw$', sensorOrientationYawPath);
      fnAsStr = fnAsStr.replace('$sensorOrientationPitch$', sensorOrientationPitchPath);
      fnAsStr = fnAsStr.replace('$sensorOrientationRoll$', sensorOrientationRollPath);

      return fnAsStr;
    },
    rangeTransformFunc(model) {
      let  rangePath = (typeof model.data === 'object') ? `rec${model.data.data.path.replaceAll('\/', '.')}` : `${model.data}`;
      const func = {
        getRange: {
          dataSourceIds: [model.dataSourceId],
          handler: function(rec) {
            return $range$;
          }
        }
      };

      let fnAsStr = stringifyFn(func);
      fnAsStr = fnAsStr.replace('$range$', rangePath);

      return fnAsStr;
    },
    fovTransformFunc(model) {
      let  fovPath = (typeof model.data === 'object') ? `rec${model.data.data.path.replaceAll('\/', '.')}` : `${model.data}`;
      const func = {
        getFov: {
          dataSourceIds: [model.dataSourceId],
          handler: function(rec) {
            return $fov$;
          }
        },
      };
      let fnAsStr = stringifyFn(func);
      fnAsStr = fnAsStr.replace('$fov$', fovPath);
      return fnAsStr;
    },
    getMapping(id) {
      let values = reactive({value: []});
      if(id) {
        this.sweStore.getDataSourceMapping(this.dashboardId, id).then(result => {
          values.value = result;
        });
      }
      return values;
    },
  }
}
</script>

<style>
.v-tooltip .v-overlay__content {
  pointer-events: auto;
  background: rgba(66,66,66,0.9);
}
</style>

<style scoped>
.opacity-slider {
  padding-top: 5px;
  overflow: hidden;
}
.info-icon {
  margin-right: 10px;
  cursor: pointer;
  z-index: 999999999999;
}
</style>
