<template>
  <dashboard id="dashApp"  ref="dashboard" :key="$route.params.windowId">
    <dash-layout
        v-for="layout in uiStore.ui.layouts[windowId]"
        v-bind="layout"
        :debug="false"
        :key="layout.breakpoint"
        :compact="false"
    >
      <dash-item
          :locked="!uiStore.ui.edit"
          v-for="item in layout.items"
          :key="item.id"
          :id="item.id"
          :x="item.x"
          :y="item.y"
          :width="item.width"
          :height="item.height"
          @resizeEnd="widgetMoveResizeEnd"
      >
        <Widget ref="widgets" :id="item.id" :key="item.id"
                @moveEnd="moveEnd" :bk="bk"
        ></Widget>
      </dash-item>
    </dash-layout>
  </dashboard>
</template>

<script>
import Widget from "./Widget.vue";
import {Dashboard, DashItem, DashLayout} from "vue-responsive-dash";
import {randomUUID} from 'osh-js/core/utils/Utils';
import {useUiStore} from "@/store/ui";
import {useClientStore} from "@/store/client";
import {useRoute} from "vue-router";

export default {
  name: "Window",
  components: {
    Widget,
    Dashboard,
    DashLayout,
    DashItem
  },
  setup(props) {
    const route = useRoute();
    let uiStore = useUiStore();
    let clientStore = useClientStore();

    // find the right layout
    const { dashboardId, windowId } = route.params;

    const uuid = randomUUID();
    let windowState = clientStore.getWindowById(dashboardId, windowId);
    // let windowState = props.windowState;
    let style;
    return {style, uiStore, uuid, clientStore, dashboardId, windowState, windowId};
  },
  computed: {
    bk() {
      return this.getBreakpoint();
    }
  },
  methods: {
    getBreakpoint() {
      return this.$refs.dashboard.currentBreakpoint;
    },
    moveEnd(event) {
      this.moveItem({
        x: event.x,
        y: event.y,
        width: event.width,
        height: event.height,
        id: event.id
      });
    },
    widgetMoveResizeEnd(event) {
      console.log('resizeEnd')
      this.moveItem({
        x: event.x,
        y: event.y,
        width: event.width,
        height: event.height,
        id: event.id
      });
    },
    moveItem(pos) {
      const widget = this.clientStore.getWidgetById(this.dashboardId, this.windowState.id, pos.id);
      widget.size = {
        width: pos.width,
        height: pos.height
      };
      widget.pos = {
        x: pos.x,
        y: pos.y
      };
      const currentBreakpoint = this.getBreakpoint();
      // TODO: pass windowId instead of entire state
      this.uiStore.editGridItemByBreakpoint(this.dashboardId, this.windowState, widget, currentBreakpoint, {
        x: pos.x,
        y: pos.y,
        width: pos.width,
        height: pos.height
      });
    }
  }
}
</script>

<style scoped>
#dashApp {
  overflow-x: auto !important;
  overflow-y: hidden !important;
  width: 100%;
  height: 100%;
  background: white;
}

.content {
  height: 100%;
  width: 100%;
  border: 2px solid #42b983;
  background-color: #42b9833d;
}

.placeholderTest {
  height: 100%;
  width: 100%;
  background-color: rgb(173, 207, 77);
  opacity: 0.2;
}
</style>
