import {defineStore} from "pinia/dist/pinia";
import DataStreamFilter from "osh-js/core/sweapi/datastream/DataStreamFilter";
import Systems from "osh-js/core/sweapi/system/Systems";
import SystemFilter from "osh-js/core/sweapi/system/SystemFilter";
import {isDefined, randomUUID} from 'osh-js/core/utils/Utils';
import {Mode} from 'osh-js/core/datasource/Mode';
import {useClientStore} from "@/store/client";
import {mapDataSourceSchemaToFieldTemplate} from "@/morpheusx/factory";
import {flattenFilter} from "@/utils";
import {useUiStore} from "@/store/ui";

let clientStore;
let uiStore;

function getClientStore() {
    if (!isDefined(clientStore)) {
        clientStore = useClientStore();
    }
    return clientStore;
}

function getUiStore() {
    if (!isDefined(uiStore)) {
        uiStore = useUiStore();
    }
    return uiStore;
}

export const useSweStore = defineStore('sweStore', {
    state: () => {
        return {
            id: randomUUID(),
            servers: {}, // serverId -> [List<System>]
            activeIntervalDataStreams: {}
        }
    },
    getters: {},
    actions: {
        async checkSystems(dashboardId, serverId) {
            if (!(serverId in this.servers)) {
                let clientStore = getClientStore();
                const server = clientStore.findServerById(dashboardId, serverId);
                // fetch systems
                const systems = new Systems({
                    protocol: 'http',
                    tls: server.tls,
                    endpointUrl: server.endpoint
                });
                const systemCollection = await systems.searchSystems(new SystemFilter({}), 10);
                const servers = {};
                while (systemCollection.hasNext()) {
                    const page = await systemCollection.nextPage();
                    for (let i = 0; i < page.length; i++) {
                        servers[page[i].properties.id] = {
                            system: page[i],
                            dataStreams: []
                        };
                    }
                }
                servers.timestamp = Date.now();
                this.servers[server.id] = servers;
            }
        },
        async getSystems(dashboardId, serverId) {
            await this.checkSystems(dashboardId, serverId);
            const results = [];
            let v;
            for (let k in this.servers[serverId]) {
                if (typeof this.servers[serverId][k] === 'object' && 'system' in this.servers[serverId][k]) {
                    v = this.servers[serverId][k].system;
                    results.push({
                        id: v.properties.id,
                        name: v.properties.properties.name
                    });
                }
            }
            return results;
        },
        async checkDataStreams(dashboardId, serverId, systemId) {
            await this.checkSystems(dashboardId, serverId);
            if (serverId in this.servers && systemId in this.servers[serverId]) {
                const dataStreams = this.servers[serverId][systemId].dataStreams;
                if (dataStreams.length === 0) {
                    const system = this.servers[serverId][systemId].system;
                    // fetch datastreams
                    const dataStreamFilter = new DataStreamFilter({});
                    const collection = await system.searchDataStreams(dataStreamFilter, 100);
                    const ds = [];
                    while (collection.hasNext()) {
                        const page = await collection.nextPage();
                        for (let i = 0; i < page.length; i++) {
                            ds.push(page[i]);
                        }
                    }
                    this.servers[serverId][systemId].dataStreams = ds;
                }
            }
        },
        async getDataStreams(dashboardId, serverId, systemId) {
            await this.checkDataStreams(dashboardId, serverId, systemId);
            return this.servers[serverId][systemId].dataStreams.map(elt => {
                return {
                    id: elt.properties.id,
                    name: elt.properties.name,
                    phenomenonTime: elt.properties.phenomenonTime
                };
            });
        },
        async getSchema(dashboardId, serverId, systemId, dataStreamId, format) {
            let dataStream;
            await this.checkSystems(dashboardId, serverId);
            await this.checkDataStreams(dashboardId, serverId, systemId);
            for (let d of this.servers[serverId][systemId].dataStreams) {
                if (d.properties.id === dataStreamId) {
                    dataStream = d;
                    break;
                }
            }
            if (dataStream) {
                // build the key based on server, dataStream and format
                const filter = new DataStreamFilter({
                    obsFormat: format
                });
                return await dataStream.getSchema(filter);
            }
        },

        setActiveUpdateDataSource(dashboardId, dataSourceId, timeSync) {
            const dataSourceConfig = getClientStore().findDataSourceById(dashboardId, dataSourceId);
            if (dataSourceConfig) {
                const {id, parentId, resource, responseFormat, type, serverId} = dataSourceConfig;

                if (type === 'SweApiStream') {
                    // SWE only
                    let dataSource;
                    for(let ds of timeSync.getDataSources()) {
                        if(ds.id === id) {
                            dataSource = ds;
                            break;
                        }
                    }
                    if(isDefined(dataSource)) {
                        const regex = new RegExp("^(.*?)\/datastreams\/(.*)\/observations");
                        let datastreamId = resource.match(regex)[2];
                        let first = true;
                        const datastreamInterval = setInterval(async () => {
                            fetch(`http${dataSource.properties.tls? 's' : ''}://${dataSource.properties.endpointUrl}/datastreams?id=${datastreamId}&select=id,phenomenonTime&f=application%2Fjson`)
                                .then(response => {
                                    if (!response.ok) {
                                        // create error object and reject if not a 2xx response code
                                        let err = new Error("HTTP status code: " + response.status)
                                        err.response = response
                                        err.status = response.status
                                        throw err
                                    }
                                    return response
                                })
                                .then(response => response.json())
                                .then(async response => {
                                    // update datastream times
                                    if(timeSync.getMode() === Mode.REPLAY) {
                                        const minTime = response.items[0].phenomenonTime[0];
                                        const maxTime = response.items[0].phenomenonTime[1];
                                        if (first) {
                                            dataSource.setMinTime(minTime);
                                            first = false;
                                        }
                                        dataSource.setMaxTime(maxTime);
                                        timeSync.minMaxChanged(first);
                                    }
                                });
                        }, 5000);
                        this.activeIntervalDataStreams[datastreamId] = datastreamInterval;
                    }
                }
            }
        },
        async getDataSourceMapping(dashboardId, dataSourceId) {
            const dataSourceConfig = getClientStore().findDataSourceById(dashboardId, dataSourceId);
            if (dataSourceConfig) {
                const {id, parentId, resource, responseFormat, type, serverId} = dataSourceConfig;

                if (type === 'SweApiStream') {
                    // SWE only
                    const regex = new RegExp("^(.*?)\/datastreams\/(.*)\/observations");
                    let datastreamId = resource.match(regex)[2];
                    const schema = await this.getSchema(dashboardId, serverId, parentId, datastreamId, responseFormat);
                    try {
                        const template = mapDataSourceSchemaToFieldTemplate(schema);

                        let results = [];
                        for (let data of template) {
                            const res = {
                                dataSource: {
                                    name: dataSourceConfig.name,
                                    id: dataSourceConfig.id
                                },
                                data: data,
                                tooltip: {}
                            };
                            // if no label, fallback using name
                            if (!'label' in res.data) {
                                res.data['label'] = res.data.name
                            }
                            res.uiLabel = `${res.data.label}  (${res.data.path})`;
                            flattenFilter(data, ['name', 'definition', 'code', 'description'], res.tooltip);
                            results.push(res);
                        }
                        return results;
                    } catch (ex) {
                        getUiStore().ui.snack = {
                            message: ex.message,
                            type: 'warning'
                        }
                    }
                }
            } else {
                throw Error(`DataSource ${dataSourceId} not found for mapping `);
            }
        }
    }
});
