import {defineStore} from "pinia";
import {useUiStore} from "./ui";
import {isDefined, randomUUID} from 'osh-js/core/utils/Utils';

export const usePopupsStore = defineStore('popupsStore', {
    state: () => {
        return {
            id: randomUUID(),
            popupWindows: {}
        }
    },
    getters: {},
    actions: {
        addPopupWindow(id, browserWindow) {
            this.popupWindows[id] = browserWindow;
        },
        isPopupWindow(id) {
          return id in this.popupWindows;
        },
        closePopups() {
            for (let key in this.popupWindows) {
                this.popupWindows[key].close();
            }
        },
        savePopupsPosition(dashboardId) {
            const uiStore = useUiStore();

            // clean up closed ones
            let newPopupWindows = {};
            for (let popupId in this.popupWindows) {
                let popup = this.popupWindows[popupId];
                if (!popup.closed) {
                    newPopupWindows[popupId] = popup;
                    uiStore.editLayout(dashboardId, popupId, {
                        pos: {
                            x: popup.screenLeft,
                            y: popup.screenTop,
                            z: 0
                        },
                        size: {
                            width: popup.innerWidth,
                            height: popup.outerHeight
                        }
                    });
                } else {
                    uiStore.removeLayoutById(dashboardId, popupId);
                }
            }
            this.popupWindows = newPopupWindows;
        }
    }
})
