<template>
  <v-text-field
    v-model="value"
    type="number"
    variant="solo"
    :label="label"
  ></v-text-field>
</template>

<script>
export default {
  name: "VTextFieldNumber",
  props: ['label'],
  data() {
    return {
      value: 0
    }
  },

  watch: {
    value() {
      this.$emit('update:modelValue', parseInt(this.value));
    }
  }
}
</script>

