<template>
    <v-text-field variant="solo" density="compact" label="Name" v-model="state.dataSource.name"></v-text-field>
    <v-select
        label="System"
        :items="systems"
        variant="solo"
        density="compact"
        item-title="name"
        item-value="id"
        return-object
        @update:modelValue="updateSystem"
        v-model="system"
    ></v-select>
    <v-select
        label="DataStream"
        :items="dataStreams"
        variant="solo"
        density="compact"
        return-object
        item-title="name"
        @update:modelValue="updateDataStream"
        v-model="datastream"
    ></v-select>
    <template v-if="state.dataSource.resource">
      <ProtocolsForm :protocols="protocols"></ProtocolsForm>
      <v-select
          label="Response Format"
          :items="responseFormats"
          variant="solo"
          density="compact"
          v-model="state.dataSource.responseFormat"
      ></v-select>
      <v-text-field variant="solo" density="compact" label="Prefetch batch duration" v-model="state.dataSource.prefetchBatchDuration" ></v-text-field>
      <v-text-field variant="solo" density="compact" label="Batch size" v-model="state.dataSource.prefetchBatchSize" ></v-text-field>
      <v-text-field variant="solo" density="compact" label="Time shift" v-model="state.dataSource.timeShift" ></v-text-field>
      <!-- wait for resource to get automatically start/end time -->
      <!--ModeForm></ModeForm-->
    </template>
</template>

<script>
import {useUiStore} from "@/store/ui";
import {computed, inject, watch} from 'vue';
import ProtocolsForm from './ProtocolsForm';
import ModeForm from './ModeForm';
import {useSweStore} from "@/store/swe";
import {useRoute} from "vue-router";

export default {
  name: "DataSourceSweForm",
  components: {
    ProtocolsForm,
    ModeForm
  },
  setup(props) {
    const uiStore = useUiStore();
    const sweStore = useSweStore();

    const route = useRoute();
    // check if the dashboard exists
    let {dashboardId} = route.params;
    const state = inject('state');

    state.dataSource.mode = 'replay';
    if(!state.dataSource.timeShift) {
      state.dataSource.timeShift = 0;
    }
    if(!state.dataSource.prefetchBatchDuration) {
      state.dataSource.prefetchBatchDuration = 5000;
    }
    if(!state.dataSource.prefetchBatchSize) {
      state.dataSource.prefetchBatchSize = 250;
    }
    // defines specific properties of SWE datasource
    state.specType = {
      resource: undefined,
      parentId: undefined,
      keywords: undefined,
      includedProps: undefined,
      excludeProps: undefined,
      roi: undefined,
      featureOfInterest: undefined,
      observedProperty: undefined,
      startTime: undefined,
      endTime: undefined
    };

    return { uiStore, state, sweStore, dashboardId};
  },
  data() {
    return {
      dataStreams: [],
      systems: undefined,
      protocols: [
        {
          title: 'WebSocket',
          value: 'ws'
        },
        {
          title: 'MQTT',
          value: 'mqtt'
        },
      ],
      responseFormats: ['application/swe+json', 'application/om+json', 'application/swe+binary', 'application/swe+csv', 'application/swe+xml'],
      system: undefined,
      datastream: undefined
    }
  },
  watch: {
    datastream(dataStream) {
      if (!this.state.dataSource.startTime) {
        // at this stage, we assign default start/end time of the datastream
        this.state.dataSource.startTime = dataStream.phenomenonTime[0];
        this.state.dataSource.endTime = dataStream.phenomenonTime[1];
      }
    }
  },
  async mounted() {
    if (!this.systems) {
      try {
        this.systems = await this.sweStore.getSystems(this.dashboardId, this.state.server.id);
        if(this.state.dataSource.parentId) {
          for(let s of this.systems) {
            if(s.id === this.state.dataSource.parentId) {
              this.system = s;
              break;
            }
          }
          if (this.system) {
            await this.fetchDataStream(this.state.dataSource.parentId);
          }
        }
      } catch (ex) {
        this.uiStore.ui.snack = {
          message: 'Cannot Fetch Systems',
          type: 'error'
        }
      }
    }
  },
  methods: {
    updateSystem(system) {
      this.state.dataSource.parentId = system.id;
      this.fetchDataStream(system.id);
    },
    updateDataStream(dataStream) {
      this.state.dataSource.resource = `/api/datastreams/${dataStream.id}/observations`;
      this.state.dataSource.name = dataStream.name;
    },
    async fetchDataStream(systemId) {
        this.dataStreams =  await this.sweStore.getDataStreams(this.dashboardId,this.state.server.id, systemId);
        if (this.state.dataSource.resource) {
          const regex = new RegExp("^(.*?)\/datastreams\/(.*)\/observations");
          let id = this.state.dataSource.resource.match(regex)[2];
          for(let d of this.dataStreams) {
            if(d.id === id) {
              this.datastream = d;
              break;
            }
          }
        }
    }
  }
}
</script>
