<template>
  <v-text-field variant="solo" density="compact" label="Name" v-model="widget.name" ></v-text-field>
  <v-select
      label="View"
      :items="items"
      variant="solo"
      v-model="selected"
      @update:modelValue="onChange"
      density="compact"
  ></v-select>
  <WidgetFormMap v-if="selected === 'Map'"></WidgetFormMap>
  <WidgetFormVideo v-else-if="selected === 'Video'"></WidgetFormVideo>
  <WidgetFormChart v-else-if="selected === 'Chart'"></WidgetFormChart>
  <TimeBarForm v-else-if="selected === 'Timebar'"></TimeBarForm>
  <WidgetFormControlMap v-if="selected === 'ControlMap'"></WidgetFormControlMap>
</template>

<script>
import WidgetFormMap from "./WidgetFormMap.vue";
import WidgetFormChart from "./WidgetFormChart.vue";
import WidgetFormVideo from "./WidgetFormVideo.vue";
import TimeBarForm from "./timebar/TimebarForm.vue";
import WidgetFormControlMap  from "@/views/forms/widget/WidgetFormControlMap.vue";

import {inject} from "vue";

export default {
  name: "WidgetFormType",
  components: {
    WidgetFormMap,WidgetFormVideo,WidgetFormChart,TimeBarForm,WidgetFormControlMap
  },
  data () {
    return {
      key: 0,
      s: false,
      selected: undefined,
      version: 0,
      items: ['Map', 'Video', 'Chart', 'Timebar', 'ControlMap'],
    }
  },
  setup(props) {
    const widget = inject('widget');
    return { widget };
  },
  beforeMount() {
    this.selected = this.widget.uiType;
  },
  methods : {
    onChange(event) {
      this.widget.uiType = event;
      this.widget.layers = [];
      this.widget.version++;
      this.widget.type = undefined;
    }
  }
}
</script>

