import {defineStore} from "pinia";
import {useClientStore} from "./client";
import {defaultGrids} from "@/store/defaults";
import {isDefined, randomUUID} from 'osh-js/core/utils/Utils';
import {klona} from "klona";
import {usePopupsStore} from "./popups";
import {Mode} from 'osh-js/core/datasource/Mode';

let clientStore;
let popupStore;

function getClientStore() {
    if(!isDefined(clientStore)) {
        clientStore = useClientStore();
    }
    return clientStore;
}
function getPopupStore() {
    if(!isDefined(popupStore)) {
        popupStore = usePopupsStore();
    }
    return popupStore;
}
export const useUiStore = defineStore('uiStore', {
    state: () => {
        return {
            id: randomUUID(),
            initialized: false,
            ui: {
                edit: false,
                connected: false,
                layouts: {},
                mode: 'replay',
                drawer: {
                    server: false,
                    datasource: false
                },
                snack: {
                    message: undefined,
                    type: undefined
                }
            },
            syncVersion: 0
        }
    },
    getters: {
        isEdit: state => state.ui.edit
    },
    actions: {
        // window from clientStore
        loadLayouts(windowsArr) {
            for (let window of windowsArr) {
                let items = [];
                for (let widgetId in window.widgets) {
                    let widget = window.widgets[widgetId];
                    items.push({
                        id: widget.id,
                        x: widget.pos.x,
                        y: widget.pos.y,
                        width: widget.size.width,
                        height: widget.size.height
                    });
                }

                const grids = klona(defaultGrids());
                this.ui.layouts[window.id] = grids;
                for (let grid of grids) {
                    grid.items = [];
                    for(let item of items) {
                        grid.items.push(item);
                    }
                }
            }
            this.initialized = true;
        },
        resetFromTimebar(dashboardId, windowId, props) {
            //
            const widgets = clientStore.getWidgets(dashboardId, windowId);
            for(let widgetKey in widgets) {
                let widget = widgets[widgetKey];
                this.hookReloadWidget('reset-from-timebar', widget);
            }
        },
        reset(dashboardId) {
            let clientStore = getClientStore();
            for(let windowId in this.ui.layouts) {
                if(getPopupStore().isPopupWindow(windowId)) {
                    delete this.ui.layouts[windowId];
                    clientStore.removeWindowById(dashboardId, windowId, false);
                } else {
                    let grids = this.ui.layouts[windowId];
                    // remove item from all grid
                    for (let grid of grids) {
                        // remove item from all grids
                        grid.items = [];
                    }

                    clientStore.removeAllWidgets(dashboardId, windowId, false);
                }
            }
        },
        setConnected(value) {
            this.ui.connected = value;
            this.updateVersion();
        },
        toggleConnect(dashboardId, value = !this.ui.connected) {
            const clientStore = getClientStore();
            if (value) {
                clientStore.connect(dashboardId);
            } else {
                clientStore.disconnect(dashboardId);
            }
            this.updateVersion();
        },
        setEditMode(value) {
            this.ui.edit = value;
            this.updateVersion();
        },
        removeLayoutById(dashboardId, layoutId) {
          delete this.ui.layouts[layoutId];
          const clientStore = getClientStore();
          clientStore.removeWindowById(dashboardId, layoutId);
          this.updateVersion();
        },
        addWindow(dashboardId, window) {
            const grids = defaultGrids();
            this.ui.layouts[window.id] = klona(grids);
            const clientStore = getClientStore();
            clientStore.addWindow(dashboardId, window);
            this.updateVersion();
            return this.ui.layouts[window.id];
        },
        editLayout(dashboardId, windowId, props) {
            this.ui.layouts[windowId].pos = props.pos;
            this.ui.layouts[windowId].size = props.size;
            const clientStore = getClientStore();
            clientStore.editWindow(dashboardId, windowId, props);
            this.updateVersion();
        },
        removeGridItem(dashboardId, windowId, widgetId) {
            let grids = this.ui.layouts[windowId];
            // remove item from all grid
            for (let grid of grids) {
                // remove item from all grids
                grid.items = grid.items.filter(item => item.id !== widgetId);
            }
            const clientStore = getClientStore();
            clientStore.removeWidget(dashboardId, windowId, widgetId);
            this.updateVersion();
        },
        editGridItemByBreakpoint(dashboardId, window, widget, layoutBreakpoint, props) {
            // update current item property
            let grids = this.ui.layouts[window.id];
            let i =0;
            for(let grid of grids) {
                for (let j = 0; j < grid.items.length; j++) {
                    let item = grid.items[j];
                    if (item.id === widget.id) {
                        this.ui.layouts[window.id][i].items[j] = {
                            ...item,
                            ...props
                        };
                        break;
                    }
                }
                i++;
            }
            const clientStore = getClientStore();
            clientStore.setWidget(dashboardId, window, widget);
            this.updateVersion();
        },
        hookReloadWidget(name, widget){
            return {
                name: name,
                widget: widget
            };
        },
        editGridItem(dashboardId, window, widget) {
            const clientStore = getClientStore();
            clientStore.setWidget(dashboardId, window, widget);
            this.hookReloadWidget('editGridItem', widget);
            this.updateVersion();
            return widget;
        },
        addGridItem(dashboardId, window, widget) {
            // create grid if no existing
            if (!(window.id in this.ui.layouts)) {
                this.loadLayouts([window]);
            }
            // add item to all grids
            let grids = this.ui.layouts[window.id];
            for (let grid of grids) {
                // because of structuredClone transform Array to Object
                if(!Array.isArray(grid.items)) {
                    grid.items = [];
                }
                // compute x/y position depending on others widgets
                let maxX = 0, maxY=0;
                for(let item of grid.items) {
                    if(item.y >= maxY) {
                        maxY = item.y + item.height;
                    }
                }
                widget.pos.y = maxY;
                grid.items.push({
                    id: widget.id,
                    x: widget.pos.x,
                    y: widget.pos.y,
                    width: widget.size.width,
                    height: widget.size.height
                });
            }
            const clientStore = getClientStore();
            clientStore.setWidget(dashboardId, window, widget);
            this.updateVersion();
        },
        addOrEditDataSource(dashboardId, serverId, props) {
            const clientStore = getClientStore();
            clientStore.addOrEditDataSource(dashboardId, serverId, props, this.ui.connected);
            this.hookReloadWidget('addOrEditDataSource', props);
            this.updateVersion();
        },
        removeDataSource(dashboardId, serverId, props) {
            const clientStore = getClientStore();
            clientStore.removeDataSource(dashboardId, serverId, props, this.ui.connected);
            this.hookReloadWidget('removeDataSource', props);
            this.updateVersion();
        },
        updateVersion() {
            this.syncVersion++;
        },
    },
})
